<template>
  <div v-if="!hidden && toggledOpen"   class="absolute  py-2 px-2   border-t-2 border-gray-400 "
                          style="background:#111a; bottom: 40%; right:30%; height:300px; width:260px;  ">

  <div class="  text-centered w-full text-gray-100  mb-4 capitalize">  Cargo </div>


  <div class="border-2 border-gray-400 overflow-y-auto" style="height:240px">
    <ItemList
    v-bind:itemArray="itemArray"
    v-bind:rightClickRowCallback="handleItemActionMenuCallback"
     context="cargo"
    />
  </div>

  </div>

</template>
<script>
import * as THREE from 'three'

import GalaxyHelper from '../../shared/lib/GalaxyHelper'
import UnitHelper from '../../shared/lib/UnitHelper'
import EquipmentHelper from '../../shared/lib/EquipmentHelper'

import ItemHelper from '../../shared/lib/ItemHelper'
import ImageHelper from '../../shared/lib/math/ImageHelper'
import TextureLoader from '../../client/js/TextureLoader'

import ItemList from './components/ItemList'



export default {
  name: 'ShipCargoPanel',
  data() {
    return {
      hidden: false,/// due to being docked
      toggledOpen: false,


      myPossessedUnit: null,

      lootingTargetUnit:null,
      itemArray: []
    }
  },
  props:['commandCallback','dataRequestCallback','handleItemActionMenuCallback'],
  components: {
    ItemList
  },
  created(){

      setInterval( this.refresh.bind(this), 2000 )

  },

  methods: {

    clickedIcon: function(clickData){


    },

    refresh: async function(){

      if(this.hidden || !this.toggledOpen){
        return
      }



      if(!this.myPossessedUnit){
        console.log('WARN: ship cargo has no possessed unit ')
        return
      }

      console.log('request items for shipcargo ')

      var results = await this.dataRequestCallback({
        requestType:'inventory',
        containerEncapsulatingUnitId: this.myPossessedUnit._id,
        containerOwnerPublicAddress: this.myPossessedUnit.owningPlayerAddress
      })

        this.itemArray = []
      this.inventoryDataChanged(results.data)



    },

    inventoryDataChanged:function(invData){

      if(invData && invData.length>0){
        this.itemArray = invData    //update our state

      }

    },

    canLootUnit: function(unit , lootedUnit ){

      if(!unit || !lootedUnit){
      //  console.log('cannot loot null.. ')
        return false
      }


      let targetIsLootable = UnitHelper.unitHasService( lootedUnit, 'looting'  )
      let targetWithinServiceRange = UnitHelper.unitsWithinServiceRange(unit, lootedUnit )

      return (targetIsLootable && targetWithinServiceRange)

    },

    lootingTargetChanged: function(unit){
      console.log('looting target changed ! ')

      this.lootingTargetUnit = unit;

      this.refresh()

    },




    entitiesChanged: function(myPossessedUnit, entities){


      this.myPossessedUnit=myPossessedUnit;

      if(UnitHelper.unitIsDockedAtStation(myPossessedUnit ))
      {
        this.setHidden(true)
      }else{
        this.setHidden(false)
      }

      //show slider bar for armor stat, shield stat
    },


    setHidden: function(hidden){
        this.hidden=hidden

    },

    setToggledOpen: function(open){
        this.toggledOpen = open;

        this.refresh()

    },


    resetPanel: function(hidden){
        this.toggledOpen = false;

        this.refresh()

    },


    playersChanged: function(myPlayer, playersOnGrid)
    {

     


    }




  }

}
</script>
