<template>
  <div class="my-1  text-white  text-xs"   >

    <div v-if="!items "><div class="px-2"> ? </div></div>
      <div v-if="items && items.length ==0"><div class="px-2">Empty</div></div>

    <div v-if="items && items.length >0">

      <div class="w-full cursor-pointer" v-for="item in items" :key="item._id"  @mousedown.right="onRowClicked(item)"  >

        <div class="w-3/4 inline-block px-2 truncate" style="line-height: normal;"> {{item.name}}  </div>
        <div class="w-1/4 text-right inline-block px-2"  style="line-height: normal;"> {{item.stackQuantity}} </div>
      </div>



    </div>


  </div>
</template>

<style>


</style>



<script>
import * as THREE from 'three'

import ItemHelper from '../../../shared/lib/ItemHelper'


export default {
  name: 'ItemList',
  props: [ 'itemArray','rightClickRowCallback' ,'context'],
  data() {
    return {
      hidden: false,
      items: []

    }
  },
  created()
  {
   this.rebuild(this.itemArray)
  },
   watch: {
      	itemArray: function(newVal, oldVal) { // watch it
           this.rebuild( newVal )
        }
      },
  methods: {

  rebuild: function( itemsArray ){

    if(!itemsArray){
      this.items = null
      return
    }



    this.items = []
    if(itemsArray){
      for(var i in itemsArray){

        console.log('rebuild', itemsArray[i])

          let itemTypeData = ItemHelper.getItemTypeByInternalName(itemsArray[i].internalName)


          itemsArray[i].name = itemTypeData.name

          this.items.push( itemsArray[i] )
        //  console.log('push', itemsArray[i])
      }

    }

  },

    onRowClicked: function(item)
    {
        console.log('on row clicked ')
  
         this.rightClickRowCallback({item:item,itemType:'item', context: this.context})

    },


    setHidden: function(hidden){
        this.hidden=hidden

    },


    onClick: function( ){
      this.clickedCallback(   )
    }



  }

}
</script>
