<template>
  <div
   v-if="!hidden"
  :style="{top: offset.top, left:offset.left}"
  style="position:absolute;background-color:#1119;z-index:99"
  class="p-2 text-white hover:text-red text-sm mb-4">
    <span class="capitalize" >   {{getMenuTitle()}}</span>
    <br>
    <button @click="command('warp')" class="block p-2" v-if="targettingData.canWarp" >
      Warp to --
    </button>
    <button @click="command('approach')" class="block p-2" v-if="targettingData.canApproach">
      Approach
    </button>
    <button @click="command('orbit')" class="block p-2" v-if="targettingData.canOrbit">
      Orbit
    </button>

    <button @click="command('lock')" class="block p-2" v-if="targettingData.canLock">
      Lock
    </button>
    <button @click="command('unlock')" class="block p-2" v-if="targettingData.canUnlock">
      Unlock
    </button>
    <button @click="command('dock')" class="block p-2" v-if="targettingData.canDock">
      Dock
    </button>
    <button @click="command('jump')" class="block p-2" v-if="targettingData.canJump">
      Jump
    </button>

    <button @click="command('startlooting')" class="block p-2" v-if="targettingData.canLoot">
      Loot
    </button>

    <div v-if="celestials && targettingData.showCelestials">
      <HoverMenu :class="{hidden: !celestials.warpgates || celestials.warpgates.length ==0}"
        title="Warp Gates"
        v-bind:optionArray="celestials.warpgates"
        v-bind:commandCallback="clickedHoverMenuOption"
        v-bind:openedCallback="clickedHoverMenu"
        ref="warpgateMenu"
       />
       <HoverMenu :class="{hidden: !celestials.planets || celestials.planets.length ==0}"
         title="Planets"
         v-bind:optionArray="celestials.planets"
         v-bind:commandCallback="clickedHoverMenuOption"
         v-bind:openedCallback="clickedHoverMenu"
         ref="planetMenu"
        />
        <HoverMenu :class="{hidden: !celestials.structures || celestials.structures.length ==0}"
          title="Structures"
          v-bind:optionArray="celestials.structures"
          v-bind:commandCallback="clickedHoverMenuOption"
          v-bind:openedCallback="clickedHoverMenu"
          ref="structureMenu"
         />
         <HoverMenu :class="{hidden: !celestials.anomalies || celestials.anomalies.length ==0}"
           title="Anomalies"
           v-bind:optionArray="celestials.anomalies"
           v-bind:commandCallback="clickedHoverMenuOption"
           v-bind:openedCallback="clickedHoverMenu"
           ref="anomalyMenu"
          />
    </div>



  </div>
</template>
<script>
import * as THREE from 'three'

import HoverMenu from './components/HoverMenu'

import GalaxyHelper from '../../shared/lib/GalaxyHelper'
import UnitHelper from '../../shared/lib/UnitHelper'

export default {
  name: 'RightClickMenu',
  props: ['commandCallback','localActionCallback'],
  components: {
     HoverMenu,
  },
  data() {
    return {
      hidden: true,
      offset: {top:0, left:0},
      targettingData: {},
      celestials: null,
      targetLocks: [],
      myPossessedUnit: null,
      grid:null
    //  warpGates: [],
    //  planets:  [],
    //  anomalies: []
    }
  },
  methods: {

    gridChanged: function( grid ){


    },

    /// need player and solar system data
    entitiesChanged: function(myPossessedUnit, entities, gridData){

      this.myPossessedUnit = myPossessedUnit;

      if(myPossessedUnit)
      {
        var myGridUUID = myPossessedUnit.grid;
        var mySolarsystemName = gridData.solarsystem;
      //console.log("rclickmenu knows im at ", myPossessedUnit , myGridUUID, mySolarsystemName)

        var celestials = GalaxyHelper.getCelestialsInSolarsystem( mySolarsystemName )

      //  console.log("celestials ", celestials)

        this.celestials = {

          warpgates:[],
          planets:[],
          anomalies:[],
          structures:[]

        }

        for(var i in celestials)
        {
          var cData = GalaxyHelper.getCelestialData(celestials[i])

          if(cData.celestialtype === 'warpgate')
          {
            this.celestials.warpgates.push(  {
              name: cData.name,
              id: cData.uuid
            })
          }
          if(cData.celestialtype === 'anomaly')
          {
              this.celestials.anomalies.push(  {
              name: cData.name,
              id: cData.uuid
            })
          }
          if(cData.celestialtype === 'planet')
          {
            this.celestials.planets.push(  {
              name: cData.name,
              id: cData.uuid
            })
          }
          if(cData.celestialtype === 'structure')
          {
              this.celestials.structures.push(  {
              name: cData.name,
              id: cData.uuid
            })
          }

          //do other types too
        }

      //  this.celestialswarpGates = warpGateOptions

      }

    },


    targetLocksChanged: function(targetLocks){
      this.targetLocks = targetLocks
    },

    setHidden: function(hidden){
        this.hidden=hidden

    },

    clickedHoverMenu: function(hoverMenuName,opened){
      this.$refs.warpgateMenu.setHidden(true);
      this.$refs.planetMenu.setHidden(true);
      this.$refs.anomalyMenu.setHidden(true);
      this.$refs.structureMenu.setHidden(true);
    },

    clickedHoverMenuOption: function(hoverMenuName,optionId){
          console.log(hoverMenuName, optionId)
          this.setHidden(true)

          if(this.myPossessedUnit && hoverMenuName === 'Warp Gates' || hoverMenuName === 'Planets' || hoverMenuName === 'Structures' || hoverMenuName === 'Anomalies')
          {
            let warpLocationVector = {x:0,y:0,z:0}


            let unitLocation = new THREE.Vector3(this.myPossessedUnit.locationVector.x,this.myPossessedUnit.locationVector.y,this.myPossessedUnit.locationVector.z)
            let unitGridUUID = this.myPossessedUnit.grid

            let newLocationVector = warpLocationVector
            let newGridUUID = optionId

            let predictAllowedToWarp = (newGridUUID.toLowerCase() != unitGridUUID.toLowerCase() || unitLocation.distanceTo( newLocationVector ) > 148  )
            if(predictAllowedToWarp){
              this.commandCallback('initiateWarp', {warpGridUUID:optionId, warpLocationVector: warpLocationVector }  ) //param needs to have the new grid uuid
            }else{
              console.log('prediction: cannot warp ')
            }


          }
    },

    setOffsets: function(offset){
        this.offset=offset
        console.log('offset',offset)
    },

    command: function(cmdName){
      console.log('command',cmdName, this.targettingData )

      var target = this.targettingData.target;
      var targetId = this.targettingData.targetId;

      if(cmdName == 'dock')
      {
         this.commandCallback('dock', {targetUnitId: targetId}  )
      }

      if(cmdName == 'jump')
      {
         this.commandCallback('jump', {targetUnitId: targetId}  )
      }

      if(cmdName == 'startlooting')
      {
         this.localActionCallback('setLootingTarget',{unit: target})
         this.commandCallback('startlooting', {targetUnitId: targetId}  )  //just for the approach
      }

      if(cmdName == 'lock')
      {
         this.localActionCallback('setPrimaryTarget',{unit: target})
         this.commandCallback('lock', {targetUnitId: targetId}  )
      }

      if(cmdName == 'unlock')
      {
         this.commandCallback('unlock', {targetUnitId: targetId}  )
      }

      if(cmdName == 'orbit')
      {
        // let orbitRadius = 200 //for now

         this.commandCallback('orbit', {targetUnitId: targetId }  )
      }

      if(cmdName == 'approach')
      {
         this.commandCallback('approach', {targetUnitId: targetId}  )
      }

      this.setHidden(true)
    },

    getMenuTitle: function()
    {
        if(this.targettingData.targetName)
        {
          return this.targettingData.targetName
        }

        return 'Navigation'
    },
    buildTargettingData: function(entity)  //entity null ?
   {

     //this is not working properly!

      if(!entity || entity.name == 'skybox'  )
      {
        this.targettingData = {
          targetName: "",
          target: null,
          targetId: null,
          canApproach: false,
          canOrbit: false,
          canDock: false,
          canJump: false,
          canLoot: false,
          canLock: false,
          canUnlock: false,
          showCelestials: true
        }
        return
      }

      var ultimateParent = entity

     // for SOME REASON the corvettes parent is teh station !?

      while(ultimateParent.parent && typeof ultimateParent.userData.basetype == 'undefined')
      {
        ultimateParent = ultimateParent.parent
      }

      console.log('ultimateParent 2', ultimateParent )

      //get services from basetype
      let otherUnitId =  ultimateParent.userData._id
      let basetype = ultimateParent.userData.basetype;
      let basetypeData = UnitHelper.getBaseTypeData( ultimateParent.userData )

      let canDock = UnitHelper.unitHasService( ultimateParent.userData, 'docking'  )

      let canJump = UnitHelper.unitHasService( ultimateParent.userData, 'jumping'  )

      let canLoot = UnitHelper.unitHasService( ultimateParent.userData, 'looting'  )

      let targetIsLocked = false

      for(var lock of this.targetLocks){
        console.log( this.myPossessedUnit , lock , otherUnitId  )
        if(this.myPossessedUnit && lock.lockComplete && lock.lockedUnitId.toString() == otherUnitId && lock.lockingUnitId.toString() == this.myPossessedUnit._id)
        {
          targetIsLocked=true
          break;
        }
      }

      console.log('build right click target data ', ultimateParent  )
        this.targettingData = {
          targetName: basetypeData.name,
          targetId: ultimateParent.userData._id,
          target: ultimateParent.userData,
          canApproach: true,
          canOrbit: true,
          canDock: canDock,
          canLock: !targetIsLocked,
          canUnlock: targetIsLocked,
          canJump: canJump,
          canLoot: canLoot,
          showCelestials: false
        }



      /*
        Approach
        if its a gate -- Jump
        if its a station -- dock
        if its a ship -- lock

      */

   },




  }

}
</script>
