   static playerOwnsUnit(player,unit)
   {
     return unit.owningPlayerAddress.toLowerCase() == player.publicAddress.toLowerCase()
   }

   static async getPlayerHomeStation(player,mongoInterface){

     let homeStationGridUUID = "86dbca36ab49ae87" //the circle, central station

     if(player.playerFaction){

       let faction = GalaxyHelper.getFactionData(player.playerFaction)
       if(faction && faction.allowsPlayers){
          homeStationGridUUID = player.homeStationGridUUID
       }

     }

     let homeGrid = await GalaxyHelper.findGridWithUUID(homeStationGridUUID, mongoInterface)

     let homeStationUnitId = await mongoInterface.findOne('units', {grid: homeGrid.uuid, unittype: 'station'  } )

      return homeStationUnitId;

   }

  static getPlayerOwnerAddressForUnit(unit)
  {
    let address = unit.owningPlayerAddress

    if(!address){
      if(  unit.aiFaction){
         address = GalaxyHelper.getFactionData(unit.aiFaction).publicAddress
      }else{
         console.trace('WARN: null owner address for ', unit )
        return null
      }


    }




    return address
  }



