 //get the ID of the unit or the station-unit that the player is inside
static async getActiveItemContainerEncapsulatingUnitIDForPlayer( player, mongoInterface )
 {

   let playerPossessedUnit = await GalaxyHelper.getPossessedUnitForPlayer(player,mongoInterface)
   //if it doesnt exist then make it.. associate it either to the ship or to the station.  Belogns to a plaer and has a max mass size

   let playerIsDocked = await GalaxyHelper.playerDockedInStation(player, mongoInterface)
   if(playerIsDocked)
   {
     return playerPossessedUnit.dockedInStation
   }else if(player.possessedUnitId){
     return player.possessedUnitId
   }else{
     console.log('WARN: Could not get active item container for ', player)
     return null;

   }



 }


  static getFacingVectorFromGridToGrid(from,to)
  {

    var fromVector = new THREE.Vector3(from.gridLocationVector.x,from.gridLocationVector.y,from.gridLocationVector.z  )

    var facing = (fromVector.clone().sub(to.gridLocationVector)).normalize().negate()
    return facing;

  }

  //returns the _id of the station their ship is docked in
  static async playerDockedInStation(player, mongoInterface)
  {
    let playerPossessedUnit = await GalaxyHelper.getPossessedUnitForPlayer(player,mongoInterface)

    return playerPossessedUnit.dockedInStation
  }


   static async getPossessedUnitForPlayer(player,mongoInterface){
     return await mongoInterface.findById('units', player.possessedUnitId)
   }




