<template>
  <div class="relative w-full  " style="min-height:400px" >
    <div v-if="!connected " class="p-12 text-center  w-1/2  " style="margin:0 auto;">

      <div v-if="!assetsLoaded && !connectionError">

        <div> Loading Assets... </div>

        <div class="breeding-rhombus-spinner " style="margin:8em auto;">
          <div class="rhombus child-1"></div>
          <div class="rhombus child-2"></div>
          <div class="rhombus child-3"></div>
          <div class="rhombus child-4"></div>
          <div class="rhombus child-5"></div>
          <div class="rhombus child-6"></div>
          <div class="rhombus child-7"></div>
          <div class="rhombus child-8"></div>
          <div class="rhombus big"></div>
        </div>

        <div> {{loadingStepName}} </div>


          </div>

      <div>
        <div v-if="assetsLoaded"  class="cursor-pointer rounded bg-green-300 text-black p-2 m-2" @click="connectToServer"> Connect </div>
      </div>

      <div v-if="connectionError">
        <div class="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative" role="alert">
          <strong class="font-bold">Error.</strong>
          <br>
          <span class="block sm:inline">{{connectionError}}</span>

        </div>
         </div>

    </div>

        <div  id="gamecanvas"  >


        </div>


          <OverviewPanel
           :class="{hidden: !connected}"
             v-bind:rightClickRowCallback="handleOverviewRowClicked"
             v-bind:localActionCallback="handleLocalActionCommand"
             v-bind:commandCallback="handleClientCommand"
           ref="overview"
           />

          <StationPanel
           :class="{hidden: !connected}"
           v-bind:commandCallback="handleClientCommand"
           v-bind:localActionCallback="handleLocalActionCommand"
           v-bind:dataRequestCallback="handleClientDataRequest"
           v-bind:devToolsCallback="handleDevToolsRequest"
           v-bind:handleItemActionMenuCallback="handleItemActionMenuCallback"
           ref="stationpanel"
           />

          <RightClickMenu
          ref="rclickmenu"
          v-bind:commandCallback="handleClientCommand"
          v-bind:localActionCallback="handleLocalActionCommand"
          />

          <ItemActionMenu
          ref="itemactionmenu"
          v-bind:localActionCallback="handleLocalActionCommand"
          v-bind:dataRequestCallback="handleClientDataRequest"
          />

          <ShipHud
          :class="{hidden: !connected}"
          ref="shiphud"
          />



          <ShipModulesPanel
          :class="{hidden: !connected}"
          v-bind:commandCallback="handleClientCommand"
          ref="shipmodulespanel"
          />

          <NavigationPanel
          :class="{hidden: !connected}"
          v-bind:commandCallback="handleClientCommand"
          v-bind:localActionCallback="handleLocalActionCommand"
          ref="navigationpanel"
          />

          <StarMap
          :class="{hidden: !connected}"
          v-bind:commandCallback="handleClientCommand"
          v-bind:localActionCallback="handleLocalActionCommand"
          ref="starmap"
          />


          <LootPanel
          :class="{hidden: !connected}"
          v-bind:localActionCallback="handleLocalActionCommand"
          v-bind:commandCallback="handleClientCommand"
           v-bind:dataRequestCallback="handleClientDataRequest"
          v-bind:handleItemActionMenuCallback="handleItemActionMenuCallback"
          ref="lootpanel"
          />


            <ShipCargoPanel
            :class="{hidden: !connected}"
            v-bind:commandCallback="handleClientCommand"
             v-bind:dataRequestCallback="handleClientDataRequest"
            v-bind:handleItemActionMenuCallback="handleItemActionMenuCallback"
            ref="shipcargopanel"
            />
  </div>
</template>

<style>

</style>
<script>
import * as THREE from 'three'

 import * as MW from '../js/meshwalk';


 //import * as dat from 'dat.gui';
 //const gui = new dat.GUI();

var gameClientStarted = false;

const clock = new THREE.Clock();

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

require('../js/OrbitControls')
import Stats from 'stats.js'

const delay = require('delay')

import SkyBox from '../js/SkyBox'
import EntityManager from '../js/EntityManager'
import GalaxyHelper from '../../shared/lib/GalaxyHelper'
import EthereumHelper from '../../shared/lib/EthereumHelper'
import PlayerController from '../js/PlayerController'
import ModelLoader from '../js/ModelLoader'
import TextureLoader from '../js/TextureLoader'
import AudioSystem from '../js/AudioSystem'
import LocalTargetManager from '../js/LocalTargetManager'
import ClientConnection from '../js/ClientConnection'

import StationPanel from './station/StationPanel'
import OverviewPanel from './OverviewPanel'
import RightClickMenu from './RightClickMenu'
import ItemActionMenu from './ItemActionMenu'
import ShipHud from './ShipHud'
import StarMap from './StarMap'
import NavigationPanel from './NavigationPanel'
import ShipModulesPanel from './ShipModulesPanel'
import LootPanel from './LootPanel'
import ShipCargoPanel from './ShipCargoPanel'

import UnitHelper from '../../shared/lib/UnitHelper'

var clientConnection = new ClientConnection()
var entityManager;
var audioSystem;
var playerController;
var localTargetManager;

var mouse = new THREE.Vector2();
var raycaster = new THREE.Raycaster();

var modelLoader;
var textureLoader;
var skyboxFactory;
var pauseRendering;

var skyMesh;
var cachedGrid;


 var camera_pivot = new THREE.Object3D()

 var useAdvancedLighting = false; //laggy


export default {
  name: 'GameCanvas',
  data() {
    return {
      assetsLoaded: false,
      renderer: null,
      scene: null,
      camera: null,
      controls: null,
      rect: null,
      connected: false,
      rightClickTargetData: {},
      connectionError: null,
      loadingStepName: ''
    }
  },
  components: {
     OverviewPanel,RightClickMenu,
     ShipHud,ShipModulesPanel,NavigationPanel,StarMap,
     StationPanel,ItemActionMenu,
     LootPanel,ShipCargoPanel
  },

  methods: {
    startGameClient: async function() {
      if(gameClientStarted)return
      gameClientStarted = true;

      MW.install( THREE );



      while(!this.assetsLoaded && typeof entityManager == 'undefined')
      {
        await delay(1000);
        console.log('assets loading...')
      }

      const RENDER_DISTANCE = 20000;
        const fov = 50; //was 75 ..whatever makes skybox look better

      this.scene = new THREE.Scene()
      this.camera = new THREE.PerspectiveCamera(
        fov,
        window.innerWidth / window.innerHeight,
        0.1,
        RENDER_DISTANCE
      )

      this.renderer = new THREE.WebGLRenderer({ antialias: true, powerPreference: "high-performance" })
      this.renderer.setSize(window.innerWidth, window.innerHeight)
      document.getElementById('gamecanvas').appendChild(this.renderer.domElement)




     this.scene.add(camera_pivot);
      camera_pivot.add( audioSystem.getListener()  )

      //  audioSystem.playSound('warpin')  //it works !

      this.controls = new MW.TPSCameraControls(
      	this.camera, // three.js camera
      	camera_pivot, // tracking object
      	this.renderer.domElement
      );

    //  this.controls = new CameraControls( this.camera, this.renderer.domElement );
    //set init

      this.controls.minDistance = 20
      this.controls.maxDistance = 520
      this.controls.dollyTo(130)

      this.controls.rotateTo(1.0, 1.0, false)

      // the 'updated' event is fired by `tpsCameraControls.update()`
      this.controls.addEventListener( 'update', () => {

      //	const cameraFrontAngle    = this.controls.frontAngle;
      //	const characterFrontAngle = keyInputControl.frontAngle;
      //	playerController.direction = cameraFrontAngle + characterFrontAngle;

      } );


    //  this.controls.update();

      //for zoom
    //  this.controls.minDistance = 9
      //this.controls.maxDistance = 90



      const color = 0xFFFFFF;
      const intensity = 2.4;
      const ambientlight = new THREE.AmbientLight(color, intensity);
      this.scene.add(ambientlight);


      //this.renderer.clearTarget(ambientlight.shadow.map);


      if(useAdvancedLighting){
        const directionallight = new THREE.DirectionalLight(color, intensity);
         directionallight.position.set(0, 10, 0);
         directionallight.target.position.set(-5, 0, 0);
         this.scene.add(directionallight);
         this.scene.add(directionallight.target);

      }




      /*
      var stats = new Stats();
      stats.setMode(0);
      stats.domElement.style.position = 'absolute';
      stats.domElement.style.left = '0';
      stats.domElement.style.top = '0';
      gamecanvas.appendChild( stats.domElement );*/

    /*  var skybox = new SkyBox().getMesh()
      skybox.name="skybox"
      camera_pivot.add(skybox);*/

      skyMesh = await skyboxFactory.getMesh("purplenebula")
    //  console.log(skyMesh)

      skyMesh.material = await skyboxFactory.getMaterial("purplenebula")

      camera_pivot.add(skyMesh);


      //this is for the rightClickMenu
      this.rect = this.renderer.domElement.getBoundingClientRect();



       gamecanvas.addEventListener('mousemove', this.onDocumentMouseMove, false);
       gamecanvas.addEventListener('mousedown', this.onMouseDown, false);
      gamecanvas.addEventListener('dblclick', this.onDoubleClick, false);
      window.addEventListener('resize', this.onWindowResize, false);
      window.addEventListener('contextmenu', this.onContextMenu, false);





      //const clientConnection = new ClientConnection()
      //clientConnection.init()



      //load player data from server

      //load entity data from server  ..await.. feed it to the entity manager


      // galaxyManager = new GalaxyManager()

      //load player and galaxy data from the server

      localTargetManager = new LocalTargetManager( this.handleClientCommand )

      entityManager = new EntityManager(playerController,this.scene,modelLoader,textureLoader,localTargetManager )

      clientConnection.setEntityManager( entityManager )

      let selectedFaction = playerController.getSelectedFaction()
      clientConnection.requestSpawn(  selectedFaction  )

      //setInterval( function(){  clientConnection.requestGridState() }.bind(this)  , 500)


            this.$nextTick(()=>{
              //refs are available next tick !

              //    entityManager.setOverview( this.$refs.overview )
                //  entityManager.setShipHud( this.$refs.shiphud )

                  //right click menu commands
                /*  this.$refs.commandWarp.addEventListener("click", function(){
                    //intersect.material.color.setHex(Math.random() * 0x777777 + 0x777777);
                    rclickmenu.style.display = "none";
                    console.log('command warp')
                  }, false);*/



                  /// this unhides the panels
                  localTargetManager.getTargettingEventEmitter().on('lootingTargetChanged', function entityListener(unit) {
                    try{
                     this.$refs.lootpanel.lootingTargetChanged( unit  )
                     this.$refs.shipcargopanel.lootingTargetChanged( unit  )
                     }catch(e){}
                   }.bind(this));

                  localTargetManager.getTargettingEventEmitter().on('primaryTargetChanged', function entityListener(unit) {
                    try{
                     this.$refs.overview.primaryTargetChanged( unit  )
                     this.$refs.shipmodulespanel.primaryTargetChanged( unit  )
                     }catch(e){}
                   }.bind(this));


                  entityManager.getEntitiesEventEmitter().on('playersChanged', function entityListener(myPlayer, playersOnGrid) {
                    try{
                     this.$refs.stationpanel.playersChanged( myPlayer, playersOnGrid  )
                     this.$refs.shiphud.playersChanged( myPlayer, playersOnGrid  )
                     this.$refs.shipmodulespanel.playersChanged( myPlayer, playersOnGrid  )
                     this.$refs.overview.playersChanged( myPlayer, playersOnGrid  )
                     this.$refs.itemactionmenu.playersChanged( myPlayer, playersOnGrid  )

                     this.$refs.lootpanel.playersChanged( myPlayer, playersOnGrid  )
                     this.$refs.shipcargopanel.playersChanged( myPlayer, playersOnGrid  )

                     }catch(e){}
                   }.bind(this));


                  entityManager.getEntitiesEventEmitter().on('entitiesChanged', function entityListener(myPossessedUnit, gridEntities, gridData) {

                      this.refreshSkyboxForGrid( gridData )

                    try{
                     this.$refs.overview.entitiesChanged( myPossessedUnit, gridEntities, gridData )
                     this.$refs.shiphud.entitiesChanged( myPossessedUnit, gridEntities, gridData )
                     this.$refs.rclickmenu.entitiesChanged( myPossessedUnit, gridEntities, gridData )
                     this.$refs.shipmodulespanel.entitiesChanged( myPossessedUnit, gridEntities , gridData )
                     this.$refs.navigationpanel.entitiesChanged( myPossessedUnit, gridEntities , gridData )

                     this.$refs.stationpanel.entitiesChanged( myPossessedUnit, gridEntities , gridData )
                     this.$refs.starmap.entitiesChanged( myPossessedUnit, gridEntities , gridData )


                     this.$refs.lootpanel.entitiesChanged( myPossessedUnit, gridEntities  , gridData )
                     this.$refs.shipcargopanel.entitiesChanged( myPossessedUnit, gridEntities , gridData  )
                    }catch(e){}
                   }.bind(this));

                   entityManager.getEntitiesEventEmitter().on('targetLocksChanged', function entityListener(targetLocks) {
                     try{
                      this.$refs.overview.targetLocksChanged( targetLocks   )
                      this.$refs.rclickmenu.targetLocksChanged( targetLocks   )
                      }catch(e){}
                    }.bind(this));

                    entityManager.getEntitiesEventEmitter().on('equipmentSlotsChanged', function entityListener(equipmentSlots) {
                      try{
                       this.$refs.shipmodulespanel.equipmentSlotsChanged( equipmentSlots   )

                       }catch(e){}
                     }.bind(this));

                   if(audioSystem)
                   {

                     entityManager.getEntitiesEventEmitter().on('entitySoundEvent', function entityListener(soundEventData) {


                        audioSystem.playSoundFromEvent(soundEventData)


                      }.bind(this));
                   }




           });




      const animate = function() {}

      this.animate()
    },

    initialize: async function( )
    {

       await this.preloadAssets()




    },

    preloadAssets: async function()
    {

        textureLoader = new TextureLoader(this.handleLoadingStepCallback)
        await textureLoader.preloadAssets();

        skyboxFactory = new SkyBox(textureLoader)
        await skyboxFactory.preloadAssets();


        modelLoader = new ModelLoader(this.handleLoadingStepCallback)
        await modelLoader.preloadAssets();

        audioSystem = new AudioSystem(this.handleLoadingStepCallback)
        await audioSystem.preloadAssets();

        this.assetsLoaded = true;
    },

    connectToServer: async function( ) {

      if(this.connected)return

      var accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });

        playerController = new PlayerController( accounts[0] )

        await new Promise ((resolve, reject) => {
            clientConnection.init(playerController, this.handleConnectionErrorMessage, function(playerData){

                playerController.acceptPlayerDataFromServer( playerData  )
                resolve()
          })
        })

        this.connected=true
        this.startGameClient()  //start this game canvas up!


    },
    animate: function() {
      const delta = clock.getDelta();



        requestAnimationFrame(this.animate)


      //  this.TPSworld.step( Math.min( delta, 0.02 ) );

        var hasControlsUpdated = this.controls.update( delta );


        //this.controls.update();

         entityManager.update(this.scene,delta*1000);

         this.updateCameraPosition()


           if(!pauseRendering){
        this.renderer.render(this.scene, this.camera)
      }else{

      //  console.log('rendering paused...')
      }

    },
    updateCameraPosition()
    {

      this.controls.minDistance = 20


      let playerData = entityManager.getMyPlayerData()
      let focusUnit = entityManager.getMyPossessedUnit()




      if(focusUnit)
      {
          let dockedInStation = focusUnit.dockedInStation

        let sceneObj =  entityManager.getSceneObjectForEntity(focusUnit._id)





        if(dockedInStation)
        {
            var dockingUnit = entityManager.getDataForEntityById( dockedInStation  )
            sceneObj =  entityManager.getSceneObjectForEntity( dockedInStation )

            //why is dockingUnit null when docked in a capsule ??
            if(dockingUnit && UnitHelper.getBaseTypeData(dockingUnit).collisionRadius > 20)
            {
              this.controls.minDistance = UnitHelper.getBaseTypeData(dockingUnit).collisionRadius

            }

        }


        if(sceneObj)
        {

          let middleLocation = new THREE.Vector3()
          middleLocation.lerpVectors( camera_pivot.position,sceneObj.position, 0.2  )

           camera_pivot.position = middleLocation//sceneObj.position


        //sceneObj.add(camera_pivot)


      //  sceneObj.add(camera_pivot)  //move the camera pivot to the focus unit
      //   this.camera.rotation =

        //   this.camera.copy(this.fakeCamera)  //copy the camera data to OrbitControls camera (a workaround https://stackoverflow.com/questions/53292145/forcing-orbitcontrols-to-navigate-around-a-moving-object-almost-working/53298655#53298655 )

        }else{
          console.log('WARN - no scene object for my focus unit' )
        }
      }


    },


    onDocumentMouseMove: function(event) {
      event.preventDefault();

    //  mouse.x = ((event.clientX - this.rect.left) / this.rect.width) * 2 - 1;
      //mouse.y = -((event.clientY - this.rect.top) / this.rect.height) * 2 + 1;

      let canvasRect = document.getElementById('gamecanvas').getBoundingClientRect()


    	mouse.x = ( event.clientX /  canvasRect.width ) * 2 - 1;
    	mouse.y = - ( event.clientY / canvasRect.height) * 2 + 1;


    },
    onContextMenu: function(event) {
      	event.preventDefault(); // no right click menu
    },
    onMouseDown: function(event) {
      event.preventDefault();

      this.hideStarMap()
      this.hideAllRightClickMenus()
    //  this.controls.handleMouseDown(event)

      var rightclick;

        if (!event) var event = window.event;
        if (event.which) rightclick = (event.which == 3);
        else if (event.button) rightclick = (event.button == 2);
      if (!rightclick) {
      //  setInterval( function() {rclickmenu.style.display = "none"} , 50)  ;
          return;
      }

      //this is offset by the scroll... not right

      let canvasRect = document.getElementById('gamecanvas').getBoundingClientRect()


      mouse.x = ( (event.clientX-canvasRect.x) / canvasRect.width ) * 2 - 1;
      mouse.y = - ( (event.clientY-canvasRect.y) / canvasRect.height ) * 2 + 1;


      raycaster.setFromCamera( mouse, this.camera);



      var clickables = entityManager.getClickableEntities()
      console.log('clickables ',clickables)
      //this.scene.children

      var intersect;
      var intersects = raycaster.intersectObjects( clickables, true );
      if(typeof intersects == 'undefined' || !intersects.length)
      {
        intersect = undefined;

      }else{
        intersect = intersects[0].object;
      }

      this.openRightClickMenuForSceneObject(intersect)

    },

    openRightClickMenuForSceneObject( tarData )
    {
      this.resetAllLootMenus()
      this.hideAllRightClickMenus()
      this.$refs.rclickmenu.buildTargettingData(  tarData )

      var top_offset = -40
      this.$refs.rclickmenu.setOffsets( {left: (event.clientX) +'px'  , top: (event.clientY + top_offset ) +'px'   }  )
      this.$refs.rclickmenu.setHidden(false)

    },

    openItemActionMenuForItem( tarData )
    {
    //  this.resetAllLootMenus()
      this.hideAllRightClickMenus()

      this.$refs.itemactionmenu.buildTargettingData(tarData)

      var top_offset = -40
      this.$refs.itemactionmenu.setOffsets( {left: (event.clientX) +'px'  , top: (event.clientY + top_offset ) +'px'   }  )
      this.$refs.itemactionmenu.setHidden(false)

    },

    hideAllRightClickMenus()
    {
      this.$refs.itemactionmenu.setHidden(true)
      this.$refs.rclickmenu.setHidden(true)
    },

    hideStarMap()
    {
      this.$refs.starmap.setToggledOpen( false )

      this.setPauseRendering(false)
    },

    async refreshSkyboxForGrid(grid){

      if(!cachedGrid || grid.uuid != cachedGrid.uuid){
        cachedGrid = Object.assign({},grid);
        let skyboxname = GalaxyHelper.getSkyboxForGrid( grid )

        skyMesh.material = await skyboxFactory.getMaterial(skyboxname)
      //  console.log('new skybox is', skyboxname)
      }


    },

    setPauseRendering(paused){
      pauseRendering = paused
    //  this.animate()
    },

    resetAllLootMenus()
    {
      this.$refs.shipcargopanel.resetPanel()
      this.$refs.lootpanel.resetPanel()
    },


    onDoubleClick: function(event) {
      event.preventDefault();


      var rightclick;

        if (!event) var event = window.event;
        if (event.which) rightclick = (event.which == 3);
        else if (event.button) rightclick = (event.button == 2);
      if (rightclick) return;

        raycaster.setFromCamera(mouse, this.camera);

      //  var facing = raycaster.ray.direction
        var facing = this.camera.getWorldDirection();
        var facingVec = new THREE.Vector3(facing.x  ,facing.y  ,facing.z   ).normalize()

        console.log('set new facing vector', facingVec)

        this.handleClientCommand('setShipDirectionVector', {vector: facingVec } )


    },


    async handleItemActionMenuCallback(tarData)
    {

      this.openItemActionMenuForItem(tarData)
    },

    async handleLoadingStepCallback(stepName)
    {

      this.loadingStepName = stepName
    },

    async handleDevToolsRequest(dataRequest)
    {
      var results = await clientConnection.requestAPIData( dataRequest )

      console.log('got response from dev tool request', results)
    },


    /*
    For complex data commands that are  going to a server, requesting data
    */
    async handleClientDataRequest( dataRequest  )
    {
      console.log('handleClientDataRequest', dataRequest)

      if(dataRequest.requestType=='walletDetails')
      {
        //just get this data from web3

        let results = await EthereumHelper.getWalletDetails( clientConnection.getPublicAddress() )

      //  this.$refs.stationpanel.walletDetailsChanged( results.data )

        return results;
      }


      var results = await clientConnection.requestAPIData( dataRequest )

      if(this.$refs.stationpanel){
        this.$refs.stationpanel.onReceivedDataRequestResponse(  results   )

      }


      return results


      //handle more request  types
    /*  if(results.requestType == 'inventory')
      {
        this.$refs.stationpanel.inventoryDataChanged( results.data )
      }

      if(results.requestType == 'orders')
      {
        this.$refs.stationpanel.ordersChanged( results.data )
      }*/

      //pop up some success window w the results for 'make market order'


    },

    /*
    For local commands that are not going to a server
    */
    async handleLocalActionCommand(cmdName, cmdParams)
    {

      if(cmdName == 'openStarmap'){
         this.$refs.starmap.setToggledOpen( true )
         this.setPauseRendering(true)
      }

      if(cmdName == 'closeStarmap'){

         this.hideStarMap()
      }


      if(cmdName == 'closeRightClickMenus'){
        this.hideAllRightClickMenus()
      }

      if(cmdName=='openShipCargoPanel'){
         this.$refs.shipcargopanel.setToggledOpen( true )
      }

      if(cmdName=='setPrimaryTarget'){
        localTargetManager.setPrimaryTarget( cmdParams )
      }

      if(cmdName=='setLootingTarget'){
        localTargetManager.setLootingTarget( cmdParams )
      }

      //local commands are intercepted
      if(cmdName == 'startsell')
      {
        this.$refs.stationpanel.openModalOfType('makeMarketOrder' , cmdParams)
      }
      if(cmdName == 'destackItem')
      {
        this.$refs.stationpanel.openModalOfType('destackItem' , cmdParams)
      }
      if(cmdName == 'trashItem')
      {
        this.$refs.stationpanel.openModalOfType('trashItem' , cmdParams)
      }

      if(cmdName == 'salvageItem')
      {
        this.$refs.stationpanel.openModalOfType('salvageItem' , cmdParams)
      }

      if(cmdName == 'startProducingFromBlueprint')
      {
        this.$refs.stationpanel.openModalOfType('produceItem' , cmdParams)
      }


      if(cmdName == 'openFittingPanel')
      {
        this.$refs.stationpanel.openSubPanel('hangarFitting', cmdParams )

      }

      if(cmdName == 'openHangarCargoPanel')
      {
        this.$refs.stationpanel.openSubPanel('hangarCargo', cmdParams  )

      }



      if(cmdName == 'buyoutOrder')
      {

        let publicAddress = clientConnection.getPublicAddress()

        let orderData = cmdParams.order


        let requestData = {
          requestType: 'buyoutOrderTx',

          orderData: orderData
        }


        var result = null
        try{
            result = await  EthereumHelper.startBuyoutOrderWeb3( {item: orderData, publicAddress: publicAddress })

        }catch(error)
        {
          console.log('buyout order error', error )
        }



            if(result && result.ethTx && result.ethTx.transactionHash)
            {


              requestData.txSuccess = true
              requestData.txHash = result.ethTx.transactionHash
              this.handleClientDataRequest( requestData )

            }else{

              requestData.txSuccess = false
              this.handleClientDataRequest( requestData )

            }



      }

      if(cmdName == 'cancelOrder')
      {

        let publicAddress = clientConnection.getPublicAddress()

        let orderData = cmdParams.order


        let requestData = {
          requestType: 'cancelOrderTx',

          orderData: orderData
        }


        var result = null
        try{
            result = await  EthereumHelper.startCancelOrderWeb3( {item: orderData, publicAddress: publicAddress })

        }catch(error)
        {
          console.log('cancel order error', error )
        }



          console.log('  start cancel2 ', result)

            if(result && result.ethTx && result.ethTx.transactionHash)
            {


              requestData.txSuccess = true
              requestData.txHash = result.ethTx.transactionHash
              this.handleClientDataRequest( requestData )

            }else{

              requestData.txSuccess = false
              this.handleClientDataRequest( requestData )

            }



      }

      if(cmdName == 'approve')
      {
        let result = await EthereumHelper.approveTokensToPayspec( clientConnection.getPublicAddress() )

      }


    },

    /*
    For simple directive commands that are  going to a server
    */
    handleClientCommand(cmdName, cmdParams  )
    {
      if(cmdName == 'setShipDirectionVector' || cmdName=='lock'){
        this.resetAllLootMenus()  //close loot menus
      }


      this.playLocalSoundForLocalCommand(cmdName,cmdParams )



      //networked commands go to server
      clientConnection.sendClientCommand(cmdName, cmdParams)

    },

    handleOverviewRowClicked(entityRow)
    {
      var sceneObject = entityManager.getSceneObjectForEntity(entityRow._id)
      if(!sceneObject) {
        console.log('WARN: cannot handle right click', entityRow, sceneObject)
        return
      }


      this.openRightClickMenuForSceneObject(sceneObject)
    },

    onWindowResize: function(event) {


      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight);

      if(this.$refs.starmap){
          this.$refs.starmap.onWindowResize( )
      }


    },





    playLocalSoundForLocalCommand : function( cmdName , cmdParams)
    {

      var soundEventData = null


      if(cmdName == 'initiateWarp')
      {


        soundEventData =  {
          playerUnit: null,
          actionUnit: null,
          soundName: 'warpdriveactive'
        }
      }

      if(cmdName == 'dock')
      {
        soundEventData =  {
          playerUnit: null,
          actionUnit: null,
          soundName: 'transmitdockingrequest'
        }
      }

      if(cmdName == 'lootall')
      {
        soundEventData =  {
          playerUnit: null,
          actionUnit: null,
          soundName: 'lootitems'
        }
      }

      if(cmdName == 'lock')
      {
        soundEventData =  {
          playerUnit: null,
          actionUnit: null,
          soundName: 'locking'
        }
      }

      if(soundEventData)
      {
        audioSystem.playSoundFromEvent(soundEventData)
      }


    },



    handleConnectionErrorMessage: function( msg ){

      this.connectionError = msg
    }

  },
  mounted() {
    //this.init()
    //this.animate()


  }
}
</script>
