<template>
  <div class="p12 flex " v-if="activeStationPanel=='production'" >

  <div class="w-1/2">


    <ProductionBlueprintsTable
      v-if="activeStationPanel == 'production'"
      v-bind:itemCompoundList="blueprintItems"
      v-bind:rightClickRowCallback="rightClickRowCallback"
      context='production'
      />



  </div>



  <div class="w-1/2">

    <ActiveProductionSlotsTable
      v-if="activeStationPanel == 'production'"
      v-bind:itemCompoundList="productionslotItems"
      v-bind:rightClickRowCallback="rightClickRowCallback"
      context='production'
      />

  </div>


</div>

</template>


<script>




import GalaxyHelper from '../../../shared/lib/GalaxyHelper'


import ItemHelper from '../../../shared/lib/ItemHelper'

import ProductionBlueprintsTable from '../components/ProductionBlueprintsTable.vue'
import ActiveProductionSlotsTable from '../components/ActiveProductionSlotsTable.vue'
export default {


  name: 'productionPanel',
  props: ['rightClickRowCallback','dataRequestCallback','stationUnitId','activeStationPanel'],
  components: {
    ProductionBlueprintsTable,ActiveProductionSlotsTable
  },
  data() {
    return {
      blueprintItems: { },
      productionslotItems: { },

      focusedRowId: null,

      needsUpdate: false
    }
  },
  mounted(){
      this.update()
      setInterval( this.update, 5000 )
  },

  methods: {



  /*  clickedButton: function(buttonName)
    {


    },

    onRowClicked: function(item)
    {
        console.log('on row clicked ')


         this.rightClickRowCallback({item:item, itemType:blueprint, context:'production'})

    },
*/

    blueprintDataChanged: function(blueprintData)
    {
      console.log('got bp data', blueprintData)


        this.blueprintItems = {blueprints: []}

        for(var bp of blueprintData)
        {
          try{

            let itemTypeData = ItemHelper.getItemTypeByInternalName( bp )

            this.blueprintItems.blueprints.push( bp )

          }catch(e){
            console.log(e)
          }

        }


    },

    productionslotDataChanged: function(productionSlotData)
    {
      console.log('got productionSlotData ', productionSlotData)


        this.productionslotItems = {slots: []}

        for(var slot of productionSlotData)
        {
          try{


            this.productionslotItems.slots.push( slot )

          }catch(e){
            console.log(e)
          }

        }


    },


    setActiveForm: function(formName)
    {

      this.activeForm = formName

    },

    setNeedsUpdate: function(needs){
      this.needsUpdate = needs

      this.update( )
    },

    update: function(){
      if(this.activeStationPanel == 'production' || this.needsUpdate) {
        this.needsUpdate=false

          this.requestBlueprintsFromAPI()
          this.requestProductionSlotsFromAPI()

        }
    },

    requestBlueprintsFromAPI: async function()
    {
      console.log('request blueprints from api ')

        var results = await this.dataRequestCallback({
          requestType:'blueprints',
        stationUnitId: this.stationUnitId

      })

        this.blueprintDataChanged(results.data)



    },

    requestProductionSlotsFromAPI: async function()
    {
       console.log('request productionslots from api ')

        var results = await this.dataRequestCallback({
          requestType:'productionslots',
        stationUnitId: this.stationUnitId

      })

        this.productionslotDataChanged(results.data)



    },

    onReceivedDataRequestResponse: function( response){

      //improves the snappiness of the hangar tab
      console.log('on re', response )

      if(response.requestType == 'startProducingItem'

      ){
          this.setNeedsUpdate(true)
      }

    }




  }

}
</script>
