import * as THREE from 'three'

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

var modelcache = {}
var loader

var modelsmap = require('../../shared/worlddata/modelsmap.json')

var preloadedAssets

/*
var preloadedAssets = ['warpgateA',
'stationC',
'shipDebrisA',
'corvette',
'capsule',
'droneA',
'droneB'

]*/


export default class ModelLoader   {


 constructor(loadingStepCallback)
 {
   THREE.Cache.enabled = false

   loader = new GLTFLoader();

   preloadedAssets = Object.keys(modelsmap)

   this.loadingStepCallback = loadingStepCallback;

 }


 async loadModel( name) {

   var cacheid =  (name)

   if(modelcache[cacheid]){

     var existing =  modelcache[cacheid].clone() //need a new id  (for what ?)
        console.log('loaded model from cache ',cacheid, existing)

     return existing
   }

   this.loadingStepCallback(name)

   var assetpath = this.lookupAssetName( name)

   var filepath = '../assets/'+assetpath

  console.log(filepath)
   return new Promise ((resolve, reject) => {

     loader.load(filepath, function ( gltf ) {

       let model = gltf.scene.clone()

       /*model.traverse((o) => {
        if (o.isMesh) o.material = material;
      });*/

       modelcache[cacheid]= gltf.scene  //do caching
       resolve( model )
     }.bind(this), undefined, function ( error ) {
       console.error( error );
       reject(error)
     } );


   })

 }


 lookupAssetName( name)
 {
   console.log('map', modelsmap, name)
     return modelsmap[name].modelpath.toString()


 }


 async preloadAssets()
 {
   for(var i in preloadedAssets)
   {
     await this.loadModel( preloadedAssets[i] )
   }


 }

}
