   static getInitialVitalsForEntityType(basetype)
   {
     let stats = StatsHelper.getInitialStatsForEntityType(basetype)
     let vitals = GalaxyHelper.getUnitBaseTypeData({basetype:basetype}).vitals


     return {energy: Math.min(vitals.energy,stats.max_energy),
       shields: Math.min(vitals.shields,stats.max_shields),
       armor: Math.min( vitals.armor, stats.max_armor ) };
   }

    static getInitialStatsForEntityType(basetype)
