    //only to be done by GridUpdater !!
    static async modifyUnitVitals(unitId, vitalsDelta, mongoInterface){

      //console.log('modifyUnitVitals', unitId)
      let unit = await GalaxyHelper.findUnitById(unitId, mongoInterface)

      if(!vitalsDelta || Object.keys(vitalsDelta).length == 0 )return
      if(!unit)return

      let oldVitals = unit.vitals
      let stats = unit.stats

      let newVitals = Object.assign({}, oldVitals)



  /*
    damage is handled in combathelper.. not here
     if(!isNaN(vitalsDelta.emDamage)){
     ///   newVitals.shields = Math.min(  Math.max(parseInt(oldVitals.shields) + parseInt(vitalsDelta.shields),0) , parseInt(stats.max_shields))
      }
      if(!isNaN(vitalsDelta.kinDamage)){
     ///   newVitals.shields = Math.min(  Math.max(parseInt(oldVitals.shields) + parseInt(vitalsDelta.shields),0) , parseInt(stats.max_shields))
   }*/




       if(!isNaN(vitalsDelta.energy)){
         newVitals.energy = Math.min( Math.max(parseInt(oldVitals.energy) + parseInt(vitalsDelta.energy),0) ,  parseInt(stats.max_energy))
       }

       if(!isNaN(vitalsDelta.shields)){
         newVitals.shields = Math.min(  Math.max(parseInt(oldVitals.shields) + parseInt(vitalsDelta.shields),0) , parseInt(stats.max_shields))
       }

       if(!isNaN(vitalsDelta.armor)){
         newVitals.armor = Math.min( Math.max(parseInt(oldVitals.armor) + parseInt(vitalsDelta.armor), 0) , parseInt(stats.max_armor))
       }


       //only update the ones we are affecting now
       let newUnit = await mongoInterface.updateAndFindOne('units',{_id: unit._id},
       {'vitals.energy': newVitals.energy,'vitals.shields': newVitals.shields,'vitals.armor': newVitals.armor })

       //  console.log('modified vitals', newUnit )
         return newUnit
    }




