         //TEST to make sure this works
       static async rebuildStatsForUnit(sUnit,mongoInterface){

         let unit = await GalaxyHelper.findUnitById( sUnit._id, mongoInterface)

         console.log('rebuildStatsForUnit', sUnit, unit )
         if(!unit){
           console.trace('tried to rebuild stats for ', unit )
         }

         let newStats =  StatsHelper.getInitialStatsForEntityType(unit.basetype)


         let equipmentSlots = await GalaxyHelper.findAllEquipmentOnShip(unit,mongoInterface)


         //const statNames = Object.keys( newStats )
         //console.log("rebuilding",statNames)


         //add item self stat bonuses
         for(var slot of equipmentSlots){

           let equipmentTypeData = GalaxyHelper.getEquipmentTypeData( slot.equipmenttype )
           let stat_bonuses_self = equipmentTypeData.stat_bonuses_self

           if(stat_bonuses_self)
           {
             let statbonuskeys = Object.keys( stat_bonuses_self  )

             for(var key of statbonuskeys){
               let bonusAmount = stat_bonuses_self[key]
               if(bonusAmount && !isNaN(bonusAmount)){

                   newStats[stattype]  = Math.max( newStats[stattype] + parseInt(bonusAmount), StatsHelper.getMinimumValueForStatType(key))

               }

             }
           }
         }


         //add item target bonuses
         let targetEngagedSlots = await GalaxyHelper.findAllEquipmentEngagedTargettingShip(unit,mongoInterface)

         for(var engagedSlot of targetEngagedSlots){

           let equipmentTypeData = GalaxyHelper.getEquipmentTypeData( engagedSlot.equipmenttype )
           let stat_bonuses_target = equipmentTypeData.stat_bonuses_target

           if(stat_bonuses_target)
           {
             let statbonuskeys = Object.keys( stat_bonuses_target  )

             for(var key of statbonuskeys){
               let bonusAmount = stat_bonuses_target[key]
               if(bonusAmount && !isNaN(bonusAmount)){
                   newStats[stattype]  = Math.max( newStats[stattype] + parseInt(bonusAmount), StatsHelper.getMinimumValueForStatType(key))
               }

             }
           }
         }


          return await mongoInterface.updateAndFindOne('units', {_id: unit._id},{ stats: newStats })


       }


    static getMinimumValueForStatType(statType){
      if(statType =="warp_strength"){
        return -100
      }
      if(statType =="max_armor"){
        return 1
      }
      if(statType =="signature_radius"){
        return 1
      } 


      return 0
    }

