


const GalaxyHelper = require('./GalaxyHelper')

const EthereumHelper = require('./EthereumHelper')

const ItemHelper = require('./ItemHelper')
const StatsHelper= require('./StatsHelper')



/*
  Use the items table as the TRUTH. Use it whenever you can... it is atomic.

   The equipmentSlots table is less reliable data
*/
module.exports =  class EquipmentHelper {





   static getSlotType(slot)
   {
     if(slot.slotId <10){
       return 'high'
     }

     if(slot.slotId <20){
       return 'mid'
     }

     if(slot.slotId <30){
       return 'low'
     }

     return 'low'


   }




  /*
    only look at ITEMS and UNIT TYPE

    checks to make sure ship actually has teh slot
    makes sure no items occupy it
  */
  static async equipmentSlotIsAvailable(unit, equipSlot, mongoInterface){

    let existing = await EquipmentHelper.findItemEquippedToShip(unit,equipSlot, mongoInterface)

    if(existing){
      return false
    }

    return true

  }

  static async findNextAvailableSlotToEquipItem(item, equippingUnit, mongoInterface ){

    //equipmentSlotIsAvailable

  //  console.log('findNextAvailableSlotToEquipItem', item, equippingUnit)
    let minSlot = 0;
    let maxSlot = 9;

    let unitTypeData = GalaxyHelper.getUnitBaseTypeData(equippingUnit)
    let unitTypeSlotSizes = unitTypeData.slots

    let equipmentTypeData = GalaxyHelper.getEquipmentTypeData( item.internalName )
  //  console.log(' eq type data', equipmenttypedata)

  //  console.log('eq type', equipmentTypeData , item )

    let equipmentSlotType = equipmentTypeData.equipment_slot

    if(!unitTypeSlotSizes[equipmentSlotType] || unitTypeSlotSizes[equipmentSlotType] < 1 ){
      return null
    }

    if(equipmentSlotType == 'high'){
      minSlot = 0;
    }

    if(equipmentSlotType == 'mid'){
      minSlot = 10;
    }

    if(equipmentSlotType == 'low'){
      minSlot = 20;
    }

    maxSlot = minSlot + (unitTypeSlotSizes[equipmentSlotType]-1)

    let possibleSlot = minSlot;
    let availableSlot = null;

    while(possibleSlot <= maxSlot){
      let isAvailable = await EquipmentHelper.equipmentSlotIsAvailable(equippingUnit, possibleSlot, mongoInterface)
      if( isAvailable ){
        availableSlot = possibleSlot
        break;
      }
      possibleSlot++;
    }

    console.log('avail', availableSlot)

    return availableSlot
  }


  static async findItemEquippedToShip(unit, equipSlot, mongoInterface){

    //var localUnit = await mongoInterface.findById('units', {_id: unit._id })

    return await mongoInterface.findOne('items', {equippedToUnit: unit._id, equippedToSlot: equipSlot} )

  }

  static async findOneEquipmentOnShip(unit,equipSlot,mongoInterface){

    return await mongoInterface.findOne('equipmentSlots', {unitId: unit._id, slotId: equipSlot} )

  }




  static equipmentRequiresPrimaryTarget(equipment){
    let equipmenttypedata = GalaxyHelper.getEquipmentTypeData(equipment.equipmenttype)


    return (equipmenttypedata.target_type != 'self' && equipmenttypedata.target_type != 'passive')

  }

  static equipmentOfTypeCanBeEngaged(equipment){
    let equipmenttypedata = GalaxyHelper.getEquipmentTypeData(equipment.equipmenttype)


    return (equipmenttypedata.target_type != 'passive')

  }

  /*
  Bulky.. try to make this more efficient

  Consider just stuffing EquipmentSlots right into the Unit object ..
  */
  static async getEquipmentSlotsOnGrid(gridUUID, mongoInterface)
  {

  //  let joinResult = await mongoInterface.findWithJoin('units', {otherCollection: 'equipmentSlots', localField: '_id' , foreignField: 'unitId', as:'finalresults'}     )

    let activeModules = []

    let unitsOnGrid = await mongoInterface.findAll('units',{grid: gridUUID, active:true,dead:false,  dockedInStation: null })


    for(var unit of unitsOnGrid){
      let mods = await mongoInterface.findAll('equipmentSlots',{unitId: unit._id})

     activeModules=  activeModules.concat(mods)
    }


    //ship modules are objects that are warped along with the ship to a new grid... auto healing on login
  //  let activeModules = await mongoInterface.findAll('equipmentSlots',{ gridUUID: gridUUID, active:true })

    return activeModules

  }


  //happens when items are equipped, unequipped, or when a unit CHANGES GRIDS or SPAWNS IN
  static async rebuildEquipmentSlotsForUnit(unit, mongoInterface){

    if(!unit)
    {
      console.log('WARN: rebuilding slots for null unit ?')
      return
    }

    let equippingUnit = await mongoInterface.findById('units',unit._id)

    let equippedItems = await mongoInterface.findAll('items', {equippedToUnit: equippingUnit._id} )

    console.log('rebuilding equipment slots for unit ',unit, equippedItems)

    await EquipmentHelper.clearAllEquipmentSlotsForUnit(equippingUnit,mongoInterface)

    for(var item of equippedItems){

      //equipmenttypes share names with the item basetypes !!  They just have different stats inside

       //all server-unreliable data but used for the client
      await mongoInterface.insertOne('equipmentSlots', {
        unitId: equippingUnit._id,
        slotId: item.equippedToSlot,
        equipmenttype: item.internalName,
      //  gridUUID: equippingUnit.grid,
        active:true, //unit player is logged in (not implemented)
        engaged:false //cycling
      } )


    }


  }

  static async clearAllEquipmentSlotsForUnit(unit, mongoInterface){

    let success = await mongoInterface.deleteMany('equipmentSlots', {unitId: unit._id} )

    return success

  }

}
