import * as THREE from 'three'


var texture;
var active;
var tilesHorizontal;
var tilesVertical;
var minTile;
var maxTile;
var loop;

var currentTile;

export default class AnimatedSpriteTexture{

  /*
  Loops forever... need to change thhat
  */

  constructor(tex, tilingData )
  {
  // note: texture passed by reference, will be updated by the update function.
    texture = tex;



      tilesHorizontal = tilingData.tilesHoriz;
      tilesVertical = tilingData.tilesVert;

      minTile = tilingData.minTile;
      maxTile = tilingData.maxTile;
      loop = tilingData.loop

      // how many images does this spritesheet contain?
      //  usually equals tilesHoriz * tilesVert, but not necessarily,
      //  if there at blank tiles at the bottom of the spritesheet.
    /*  this.numberOfTiles = tilingData.tilesHoriz*tilingData.tilesVert;
      if(tilingData.numberOfTiles){
        this.numberOfTiles = tilingData.numberOfTiles
      }*/

      texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set( 1 / tilesHorizontal, 1 / tilesVertical );

      // how long should each image be displayed?
    //  this.tileDisplayDuration = tileDispDuration;

      // how long has the current image been displayed?
    //  this.currentDisplayTime = 0;

      // which image is currently being displayed?
      currentTile = minTile;
      active = true

  }




     updateOneTick()
    {

      if(!active){
        return false
      }


      if (currentTile == maxTile){
        if(loop){
          currentTile = minTile; //repeat from start
        }else{
          active=false
        }

      }


      var currentColumn =  currentTile % tilesHorizontal;
      let offX =  ( currentColumn / tilesHorizontal)


      //somehow these magic numbers make it work perfect... lol OK
      var currentRow = 1+ Math.floor( currentTile / tilesHorizontal );
      let offY = 1- ( (currentRow ) / tilesVertical )


      texture.offset.set( offX, offY   )

      currentTile = currentTile+1;

    /*  let logData = {
        currentColumn: currentColumn,
        currentRow: currentRow,
        tilesHorizontal: tilesHorizontal,
        tilesVertical: tilesVertical,
        currentTile: currentTile,
        textureoff: texture.offset,

        textureoffy:texture.offset.y  //how is this not correct !?
      }

       console.log('update anim sprite one tick ',logData)

       */

      return true
  };


  getUpdateCallback( ){
    return this.updateOneTick //pass the function
  }
 

}
