<template>
  <div v-if="!hidden"   class="   py-2 px-4    " style="background:#111a;  ">

  <div   class="text-centered w-full text-gray-100 text-sm cursor-pointer mb-0 capitalize"> Target Name    </div>
  <br>
  <div class="flex">

    <div class="w-1/2">

  <div class="bg-blue-500 text-xs text-white px-1" style=" height:20px;"> Shields {{compiledStats.currentShields}}/{{compiledStats.maxShields}} </div>
   </div>
     <div class="w-1/2">
  <div class="bg-gray-700 text-xs text-white px-1" style=" height:20px;"> Armor {{compiledStats.currentArmor}}/{{compiledStats.maxArmor}} </div>
    </div>



