<template>
  <div  v-if="!hidden" class="absolute p-0 m-0" style="background:#111a; width:80%; height:80%; margin:   auto;top: 0; left: 0; bottom: 0; right: 0; ">


    <div class="m-4 p-4 h-full" style="margin:auto">


       <div class="flex h-full bg-gray-800 font-roboto">

        <Sidebar
        ref="sidebar"
         v-bind:buttonClickedCallback="sidebarButtonClicked"
        />

        <div class="flex-1 flex flex-col overflow-hidden">

          <Header class="hidden"/>

          <div class="flex-1 overflow-x-hidden overflow-y-auto bg-gray-900 relative">
            <div class="container mx-auto px-6 py-8 ">




                <div class="text-centered w-full text-gray-100">

                   <InventoryPanel

                    v-bind:rightClickRowCallback="handleItemActionMenuCallback"
                    v-bind:dataRequestCallback="dataRequestCallback"
                    v-bind:stationUnitId="stationUnitId"
                    v-bind:activeStationPanel="activePanel"

                    ref="inventorypanel"
                    />

                    <HangarPanel
                     v-bind:stationPanelIsHidden="hidden"
                     v-bind:stationUnitId="stationUnitId"
                     v-bind:myPossessedUnit="myPossessedUnit"
                     v-bind:activeStationPanel="activePanel"
                     v-bind:rightClickRowCallback="handleItemActionMenuCallback"
                     v-bind:dataRequestCallback="dataRequestCallback"

                      ref="hangarpanel"
                     />


                     <ProductionPanel

                      v-bind:rightClickRowCallback="handleItemActionMenuCallback"
                      v-bind:dataRequestCallback="dataRequestCallback"
                      v-bind:stationUnitId="stationUnitId"
                      v-bind:activeStationPanel="activePanel"

                      ref="productionpanel"
                      />

                    <WalletPanel

                     v-bind:dataRequestCallback="dataRequestCallback"
                     v-bind:localActionCallback="localActionCallback"
                     v-bind:activeStationPanel="activePanel"
                     ref="walletpanel"
                     />


                     <MarketPanel

                      v-bind:rightClickRowCallback="handleItemActionMenuCallback"
                      v-bind:dataRequestCallback="dataRequestCallback"
                      v-bind:stationUnitId="stationUnitId"
                      v-bind:activeStationPanel="activePanel"
                      v-bind:myPlayer="myPlayer"
                      ref="marketpanel"
                      />

                      <DevPanel

                       v-bind:devtoolsCallback="devToolsCallback"
                       v-bind:activeStationPanel="activePanel"
                       />


                </div>



              </div>

              <ModalContainer
              ref="modalContainer"
              v-bind:formSubmittedCallback="modalFormSubmitted"
              />




            </div>
          </div>

        </div>

        <div>



        </div>

      </div>




    </div>


</template>
<script>
/*

Will have subpanels for:

Your item storage (make folders for the user?)

Production  --certain modules can only be built in certain areas of space
Recycling
Fitting panel - upper half is your ships slots, lower half is your items (rows)


*/



import GalaxyHelper from '../../../shared/lib/GalaxyHelper'
import Sidebar from './components/Sidebar.vue'
import Header from './components/Header.vue'
import ModalContainer from './components/ModalContainer.vue'

import UnitHelper from '../../../shared/lib/UnitHelper'


import InventoryPanel from './InventoryPanel.vue'
import HangarPanel from './HangarPanel.vue'
import ProductionPanel from './ProductionPanel.vue'
import MarketPanel from './MarketPanel.vue'
import WalletPanel from './WalletPanel.vue'
import DevPanel from './DevPanel.vue'
//import RecyclingPanel from './RecyclingPanel.vue'



export default {

  name: 'StationPanel',
  components: {
     Sidebar,Header,ModalContainer,
     InventoryPanel,HangarPanel,MarketPanel, ProductionPanel,
     DevPanel, WalletPanel
  },
  props: ['commandCallback','dataRequestCallback','devToolsCallback','handleItemActionMenuCallback','localActionCallback'],
  data() {
    return {
      myPlayer: null,
      hidden: true,
      activePanel: 'hangar',
      stationUnitId: null,
      myPossessedUnit: null
    }
  },
  mounted() {

  },
  methods: {


    setHidden: function(hidden){
        this.hidden=hidden
        //console.log('station panel is hidden: ', hidden)
    },

    /*handleRequestingAPIData: function(dataRequest){
          this.dataRequestCallback(dataRequest)
    },*/

    entitiesChanged: function(myPossessedUnit, entities){
      this.myPossessedUnit=myPossessedUnit
       

      if(UnitHelper.unitIsDockedAtStation(myPossessedUnit ))
      {
        this.setHidden(false)
      }else{
        this.setHidden(true)
      }

      this.stationUnitId = myPossessedUnit.dockedInStation

    },



    playersChanged: function(myPlayer, playersOnGrid)
    {
      this.myPlayer = myPlayer





    },

    sidebarButtonClicked: function(buttonName)
    {


      if(buttonName == 'undock')
      {
        this.commandCallback('undock')

      }else{

        this.setActivePanel( buttonName )

      }

      this.localActionCallback('closeRightClickMenus')


    },

    modalFormSubmitted : function(formData)
    {
      console.log( 'modalFormSubmitted' , formData )

      //if(formData.requestType == 'makeMarketOrder'){
          this.dataRequestCallback( formData )

      //    web3Connection.createMarketOrder( formData , this.myPlayer)
      //}

        this.$refs.modalContainer.setHidden(true)
    },

    setActivePanel: function(panelName)
    {
      console.log("set panel ", panelName)
      this.$refs.sidebar.setActivePanel( panelName )
      this.activePanel = panelName


          //poll right away
          if(this.activePanel == 'inventory'){
            this.$refs.inventorypanel.setNeedsUpdate(true)
          }

          if(this.activePanel == 'hangar'){
            this.$refs.hangarpanel.setNeedsUpdate(true)
            this.$refs.hangarpanel.closeSubPanels()
          }

          if(this.activePanel == 'market'){
            this.$refs.marketpanel.setNeedsUpdate(true)
          }

          if(this.activePanel == 'wallet'){
            this.$refs.walletpanel.setNeedsUpdate(true)
          }

          if(this.activePanel == 'production'){
            this.$refs.productionpanel.setNeedsUpdate(true)
          }


    },

    openSubPanel: function(name, params){
      if(name == 'hangarFitting'){

        this.$refs.hangarpanel.openSubPanel(name,params)
      //  this.setActivePanel('hangar')
      }
      if(name == 'hangarCargo'){

        this.$refs.hangarpanel.openSubPanel(name,params)
      //  this.setActivePanel('hangar')
      }
    },


    openModalOfType: function( modalType, tarData ){
      this.$refs.modalContainer.setHidden(false)
      this.$refs.modalContainer.setActiveForm(modalType)
      this.$refs.modalContainer.setTargetData(tarData)
    },

/*


*/

    onReceivedDataRequestResponse: function( response){

      //improves the snappiness of the hangar tab
      if(this.activePanel == 'hangar' && this.$refs.hangarpanel){
        this.$refs.hangarpanel.onReceivedDataRequestResponse(response )
      }

      if(this.activePanel == 'inventory' && this.$refs.inventorypanel){
        this.$refs.inventorypanel.onReceivedDataRequestResponse(response )
      }

      if(this.activePanel == 'production' && this.$refs.productionpanel){
        this.$refs.productionpanel.onReceivedDataRequestResponse(response )
      }


    }




  }

}
</script>
