async updateTargetLockSprites( targetLocks )
{


  //mark all stale
  for (const [key, value] of spawnedTargetReticles.entries()) {
  //  await this.removeTargetReticle(key,value)
    var entry = spawnedTargetReticles.get(key)
      entry.stale = true
    spawnedTargetReticles.set(key, entry)
  }


  let myUnit = this.getMyPossessedUnit()

  if(myUnit){



    let myUnitId = myUnit._id

    entitiesEmitter.emit('targetLocksChanged',  targetLocks )



     //work on this later

    for(var lock of targetLocks){


      if(lock.lockingUnitId == myUnitId && lock.lockComplete){

        //  console.log('found matching lock', lock)

        //spawn a tergetReticle



        let lockedUnitId = lock.lockedUnitId
        let lockedUnit = currentGridEntities.get( lockedUnitId )


        //set the entry to not stale since unit is still locked
        let tRetEntry = spawnedTargetReticles.get( lockedUnitId   )
        if(tRetEntry){
          tRetEntry.stale=false
          spawnedTargetReticles.set( lockedUnitId , tRetEntry )
        }

        let isPrimaryTarget = false
        let primaryTargetUnitId = this.localTargetManager.getPrimaryTargetUnitId()
        if(lockedUnitId == primaryTargetUnitId){
          isPrimaryTarget=true
        }

            //console.log('tret  ', tRetEntry)
        if(lockedUnit){

          let lockedTargetRet = spawnedTargetReticles.get( lockedUnitId)

          var sceneObj = sceneObjectsMap.get(lockedUnit.sceneObjectUUID)
          if(sceneObj &&  (lockedTargetRet == null || lockedTargetRet.isPrimaryTarget!= isPrimaryTarget ) ){

            if(lockedTargetRet&&lockedTargetRet.sprite){
              let parent = lockedTargetRet.sprite.parent
              if(parent){
                  parent.remove( lockedTargetRet.sprite );
              }

            }


              let sprite = await targetReticleFactory.makeReticle( isPrimaryTarget )

              sceneObj.add(   sprite )


              //sprite.position.set( sceneObj.position.x,sceneObj.position.y,sceneObj.position.z   )

              let scaleFactor = 10;
              sprite.scale.set(scaleFactor,scaleFactor,scaleFactor)
              sprite.renderOrder = 101

              //console.log('spawn reticle')
              spawnedTargetReticles.set( lockedUnitId , {sprite: sprite, isPrimaryTarget:isPrimaryTarget , stale:false} )
          }
        }



      }

    }


  }

  await this.removeAllStaleTargetReticles()


}

async loadEquipmentSlots (equipmentSlots)
{
  //need to store this so that on updates , bullets are spawned and sounds are played (sounds first)
  let myUnit = this.getMyPossessedUnit()

  if(myUnit){

    let myUnitId = myUnit._id

    entitiesEmitter.emit('equipmentSlotsChanged',  equipmentSlots )

  }



  this.examineEquipmentChangesForEvents( equipmentSlots  )

}



//OPTIMIZATION: probably want each grid to be a socket channel, this data will automatically be broadcasted by the server-- not asked for
 async receivedGridStateFromServer(data)
 {

   //console.log('received grid state from server', data )

   var newGridEntities = data.entities;
   var gridUUID = data.grid.uuid;
   var newGridPlayers = data.players;

   //need to integrate these
   var targetLocks = data.targetLocks;
   var equipmentSlots = data.equipmentSlots;

   currentGridUUID  =   gridUUID
   currentGridTick = data.grid.gridTick




   await this.loadGrid( data.grid, newGridEntities, newGridPlayers  )

   await this.loadEquipmentSlots(  equipmentSlots )

   await this.updateTargetLockSprites(  targetLocks )

   await this.updateAnimatedSpritesForUnits( newGridEntities )
 }


