
const itembasetypes = require('../../shared/worlddata/items/itembasetypes.json')


 const MongoHelper = require('./util/MongoHelper')

 const GalaxyHelper = require('./GalaxyHelper')


//NOT ALLOWED to include unit helper here.. or any others for that matter

module.exports =  class ItemHelper   {

 constructor()
 {

 }

 static getItemTypeByInternalName(internalname)
 {
   return itembasetypes[internalname] ;
 }

 static allowedToSellItem( item )
 {
   return ItemHelper.allowedToMoveItem(item)// !ItemHelper.itemIsEscrowedForMarketOrder(item) && !ItemHelper.itemHasRemainderStackPending(item)


 }

 static allowedToMoveItem( item )
 {
   if( ItemHelper.itemIsEscrowedForMarketOrder(item) ){return false}
   if( ItemHelper.itemIsEscrowedForProductionSlot(item) ){return false}
   if( ItemHelper.itemHasRemainderStackPending(item) ){return false}
   if( ItemHelper.itemHasSalvagingPending(item) ){return false}
   if( ItemHelper.itemHasStackAbsorbtionPending(item) ){return false}

   //if it is being stacked/unstacked/salvaged/made from salvage/ taken for production

   return true
 }

 static itemIsEscrowedForMarketOrder( item )
 {
   return item.escrowedForMarketOrder != null

 }
 static itemIsEscrowedForProductionSlot( item )
 {
   return item.escrowedForProductionSlot != null

 }


 static getAllowedToMoveQueryById( itemId ){

   return ItemHelper.getAllowedToMoveQueryConcatenation({_id: itemId})

   /*
     escrowedForMarketOrder: null,
     escrowedForProductionSlot: null, //production slot is a new table
     equippedToUnit: null,
     stackingIntoItemId: null,
     absorbingStackItemId:null,
      quantityToSalvage:null,
      remainingQuantityToSpawn:null}*/
 }

//ex: {_id: 1}
 static getAllowedToMoveQueryConcatenation( initialQuery ){

   let safeQuery =  Object.assign({}, initialQuery); //clone
   safeQuery.escrowedForMarketOrder = null
   safeQuery.escrowedForProductionSlot = null
   safeQuery.equippedToUnit = null
   safeQuery.stackingIntoItemId = null
   safeQuery.absorbingStackItemId = null
   safeQuery.quantityToSalvage = null
   safeQuery.remainingQuantityToSpawn = null
    return safeQuery;

 }




 static getMaxStackSize(  )
 {
   return 5000

 }

 static async getContainerForItem(item, mongoInterface)
 {
   return await mongoInterface.findById('itemContainer', item.containerId  )

 }


/*
Remember to provide a unique spawnLockHash or this will fail at the db atomically



item = {
  internalName: '',
  stackQuantity:1 ,
  spawnLockId: xxxxx,

  remainingQuantityToSpawn: null,
  childStackLockId: null,

  escrowedForMarketOrder:null

  escrowedForProductionSlot:null

  equippedToUnit: null
  equippedToSlot: null

  absorbingStackItemId: null
  stackingIntoItemId: null

  quantityToSalvage: null
  beingSalvagedFromItemId: null

}
*/
 static async spawnNewItem(  newItemData, mongoInterface)
 {

   if(!mongoInterface){
     console.log('WARN: cannot spawn item, mongoInterface null')
     return null
   }

   if(!newItemData.spawnLockId){
     console.trace("WARN: Tried to spawn item with null spawnLockId")
     return null
   }


   if(  newItemData.containerId && newItemData.internalName && newItemData.stackQuantity && newItemData.stackQuantity > 0  )
   {
     newItemData.escrowedForMarketOrder = undefined;


     let newItem = await mongoInterface.insertOne('items',  newItemData   )
     return newItem
   }else{
     console.log('WARN: could not spawn item ', newItemData)
   }
   return null


 }


 static async getItemsWithinContainer( container, mongoInterface )
 {

   //console.log('get items within ', container )
   let items = await mongoInterface.findAll('items',
   {containerId: container._id,
     escrowedForMarketOrder: undefined ,
  //   remainingQuantityToSpawn:null, //cant move item so may as well not show it ?
      equippedToUnit: undefined,

      } )


   //console.log('found', items)
   return items;
 }



 static async transferOneItemToContainer(item, newContainer, mongoInterface){

   let newContainerId = newContainer._id

   if(!MongoHelper.isValidObjectId(newContainerId)){
     console.trace('Error: cannot transfer item to container with invalid id ', newContainerId)
     return null
   }

   if(!ItemHelper.allowedToMoveItem(item)){
     console.trace('Error: cannot transfer item while it is in that state ', item)
     return null
   }

   //mark the item and then call a method that has auto healing...
   return await mongoInterface.updateAndFindOne('items', ItemHelper.getAllowedToMoveQueryById(  item._id ) , {containerId: newContainerId }  )
 }


   static async spawnRemainderStackForItem(item,mongoInterface){
   //  console.log('meep spawn remainder stack' , item )

     if(!item){
       console.trace('Could not spawn remainder stack for ', item )
       return null
     }

     let remainingQuantity = item.remainingQuantityToSpawn


     if(item.remainingQuantityToSpawn && !item.childStackLockId){
       console.trace('WARN: Item has remainingQuantityToSpawn but no childStackLockId!! ', item )
       return
     }

     if(remainingQuantity == 0)
     {
       //do data healing !!!
       await mongoInterface.updateCustomAndFindOne('items', {_id: item._id}, { $unset: {remainingQuantityToSpawn: undefined, childStackLockId:undefined } }    )

      // await mongoInterface.updateOne('items', {_id: item._id} , {  remainingQuantityToSpawn: undefined, childStackLockId:undefined  } )
       return
     }

     if(remainingQuantity > 0  )
     {

       try{
              let remainderItem = await ItemHelper.spawnNewItem({

                containerId: item.containerId,
                internalName: item.internalName,
                stackQuantity: remainingQuantity,
                spawnLockId: item.childStackLockId   //indexed and unique so no dupes can happen

              }, mongoInterface)
          }catch(e)
          {
            console.trace(e)
          }

      let newStackExists = await mongoInterface.findOne('items',{spawnLockId: item.childStackLockId})

    //  console.log('remainder item' ,remainderItem )
      if(newStackExists){
        //this is the ONLY reason an items remainder stack should go null
        await mongoInterface.updateCustomAndFindOne('items', {_id: item._id}, { $unset: {remainingQuantityToSpawn: undefined, childStackLockId:undefined } }    )

      //  await mongoInterface.updateOne('items', {_id: item._id} , {  remainingQuantityToSpawn: undefined, childStackLockId:undefined  } )

      }

    }


   }

   static itemHasStackAbsorbtionPending(item){
     return (item.absorbingStackItemId != null || item.stackingIntoItemId!=null)
   }

   static itemHasSalvagingPending(item){
     return (item.quantityToSalvage != null || item.beingSalvagedFromItemId != null)
   }

   //dont let the item be moved or used if it has a pending remainder stack!!
   static itemHasRemainderStackPending(item)
   {
     return (item.remainingQuantityToSpawn != null )  //should never become 0
   }



  static async removeItemContainer( itemContainer, mongoInterface ){

    await mongoInterface.deleteMany('items', {containerId: itemContainer._id})

    await mongoInterface.deleteOne('itemContainer', {_id: itemContainer._id})


  }



  //only works for ships, not for stations or things you dock inside of
static async getContainerWithinUnit(unit, mongoInterface){

//  let owningPlayer = await GalaxyHelper.findPlayerByAddress(unit.owningPlayerAddress)
  let pubAddress = GalaxyHelper.getPlayerOwnerAddressForUnit(unit);

  return await ItemHelper.getContainerFromPlayerAndUnitOwner(pubAddress, unit._id, mongoInterface)

}


//find or create
//containerOwnerId is a mongo object id BUT doesnt have to be
 static async  getContainerFromPlayerAndUnitOwner(publicAddress, containerOwnerId, mongoInterface)
 {

   publicAddress = publicAddress.toLowerCase()

   if(!containerOwnerId /*|| typeof containerOwnerId != 'object'*/){
     console.log('WARN: Cannot get container with invalid containerOwnerId')
     return null
   }

   if(!publicAddress){
     console.log('WARN: Cannot create container with null publicAddress')
     return null
   }

  // console.log('getContainerFromPlayerAndUnitOwner',containerOwnerIdRaw,publicAddress)

   let containerOwnerUnit = await mongoInterface.findById('units', containerOwnerId)

   if(containerOwnerUnit)
   {
      let ownerUnitId = containerOwnerUnit._id
     //let containerOwnerId = containerOwnerUnit._id;

     let existingcontainer = await mongoInterface.findOne('itemContainer', {ownerAddress: publicAddress, encapsulatingUnitId:ownerUnitId  } )

     if(!existingcontainer)
     {
           await mongoInterface.insertOne('itemContainer', {ownerAddress: publicAddress, encapsulatingUnitId:ownerUnitId  } )
         existingcontainer = await mongoInterface.findOne('itemContainer', {ownerAddress: publicAddress, encapsulatingUnitId:ownerUnitId  })

     }

     return existingcontainer;
   }else{
     console.log('WARN: could not find container for ', publicAddress, containerOwnerUnit)
     return null
   }

 }




   static async playerAddressHasAccessToContainer(publicAddress, inputContainer, mongoInterface )
   {
     let player = await mongoInterface.findOne('activePlayers', {publicAddress: publicAddress } )

     if(player)
     {

       let container = await mongoInterface.findById('itemContainer', inputContainer._id)

       let playerOwnsContainer = (container.ownerAddress.toLowerCase() == player.publicAddress.toLowerCase())

       let playerUnit = await mongoInterface.findById('units', player.possessedUnitId)


       let containerOwnerId = container.encapsulatingUnitId;


        let containingUnit = await mongoInterface.findOne('units', {_id: containerOwnerId } )



       if(!containingUnit)
        {
          console.log("WARN: null containingUnit for playerAddressHasAccessToContainer")
          return false
        }

        if( playerOwnsContainer ){

          //if the container is owned by the unit that the player is possessing
           if(MongoHelper.documentIdsAreEqual( player.possessedUnitId, containerOwnerId ) ){return true}

           let playerisDockedInStation = await GalaxyHelper.playerDockedInStation(player,mongoInterface)

          //if the player is docked  and the container is owned by the station they are docked in
          if(playerisDockedInStation && MongoHelper.documentIdsAreEqual(playerisDockedInStation, containerOwnerId ) ){return true}


          //if the ship is docked in station, if the player owns it, and if the player is in the same station...
          if(playerisDockedInStation   && containingUnit.dockedInStation
            && GalaxyHelper.playerOwnsUnit(player,containingUnit)
             && MongoHelper.documentIdsAreEqual(containingUnit.dockedInStation, playerisDockedInStation ) ){return true}


        }

          //if the players possessed unit is looting the containing unit ...
        if(MongoHelper.documentIdsAreEqual( playerUnit.lootingUnitWithId, containerOwnerId  )){return true}


      }


     console.log('WARN: DOES NOT HAVE ACCESS', publicAddress, container)

     return false;
   }




}
