<template>
  <div >


    <form class="w-full max-w-lg"  v-if="targetData" >
  <div class="flex flex-wrap   ">
    <div class="w-1/2">


    <div class="w-full  px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-200 text-xs font-bold mb-2" for="grid-first-name">
        Item Name
      </label>
      <input disabled class="appearance-none  block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"  type="text" v-model="itemToProduce.name ">
      <p class=" hidden text-red-500 text-xs italic">Please fill out this field.</p>
    </div>
    <div class="w-full   px-3">
      <label class="block uppercase tracking-wide text-gray-200 text-xs font-bold mb-2" for="grid-last-name">
        Quantity To Produce
      </label>
      <input class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"  type="text" v-model="quantityToProduce">
    </div>

      </div>
      <div class="w-1/2">
          <div> Input </div>


          <ItemList
          v-bind:itemArray="inputItemArray"
          v-bind:rightClickRowCallback="rightClickRowCallback"
           context="production"
          />

        </div>
  </div>


  <div >
     <button @click="submit" class="float-right bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline cursor-pointer hover:bg-yellow-500" type="button">
        Produce Item
      </button>
  </div>

</form>

  </div>
</template>


<script>
import GalaxyHelper from '../../../../shared/lib/GalaxyHelper'
import ItemList from '../../components/ItemList'
import ProductionHelper from '../../../../shared/lib/ProductionHelper'


export default {
  name: 'ProductionForm',
  props: ['formSubmittedCallback','targetData'],
  components: {
    ItemList
  },
  data() {
    return {
      itemToProduce: null,
      quantityToProduce: 1,
      inputItemArray: null
    }
  },
  created()
  {
    this.rebuild()
  },
  watch: {
   quantityToProduce: function (val) {
       this.rebuild()
   }
 },
  methods: {
    rightClickRowCallback: function(){
    },
    rebuild: function(){
        this.inputItemArray=null

        this.itemToProduce = this.targetData.item   ///blueprint

          console.log('rebuild', this.itemToProduce )

        if(this.itemToProduce && !isNaN(this.quantityToProduce) && parseInt(this.quantityToProduce) >=1 ){

            let baseInput = ProductionHelper.getItemsInputForProduction( this.itemToProduce )


          console.log('baseinput', baseInput)
          if(baseInput){
                this.inputItemArray=[]
            for(var row of baseInput){
              row.stackQuantity = row.quantity * parseInt(this.quantityToProduce);
              this.inputItemArray.push(row)
            }
          }
        }
    },
    submit: function()
    {
      let formData = {
        requestType: 'produceItem',
        item: this.itemToProduce,
        quantity: this.quantityToProduce
      }
      this.formSubmittedCallback( formData  )
      this.reset()
    },
    reset: function()
    {
      this.itemToProduce=null;
      this.quantityToProduce=0;
    },
  }
}
</script>
