<template>
  <div class="p12" v-if="activeStationPanel=='inventory'" >

    <div class="text-lg"> Inventory   </div>


    <ItemTable
      v-bind:itemCompoundList="itemCompoundList"
      v-bind:rightClickRowCallback="rightClickRowCallback"
      context='inventory'

    />




  </div>
</template>


<script>




import GalaxyHelper from '../../../shared/lib/GalaxyHelper'


import ItemHelper from '../../../shared/lib/ItemHelper'

import ItemTable from '../components/ItemTable'


var requestTimer;
export default {


  name: 'InventoryPanel',
  props: ['rightClickRowCallback','dataRequestCallback','stationUnitId','activeStationPanel'],
  components: {
    ItemTable
  },
  data() {
    return {
      itemCompoundList: [],
      focusedRowId: null,

      needsUpdate: false
    }
  },
  created(){
      this.requestItemsFromAPI()
      requestTimer = setInterval( this.requestItemsFromAPI, 5000 )
  },
  beforeDestroy(){

      clearInterval( requestTimer )
  },
  methods: {



  /*  clickedButton: function(buttonName)
    {


    },

    onRowClicked: function(item)
    {
        console.log('on row clicked ')



         this.rightClickRowCallback({item: item, itemType:'item', context:'inventory'})

    },*/


    inventoryDataChanged: function(inventoryData)
    {
      this.itemCompoundList = null
        var itemRows = []

        for(var i in inventoryData)
        {
          try{

            let typedata = ItemHelper.getItemTypeByInternalName(inventoryData[i].internalName)

            //  console.log('got inv type ', typedata)

            let row = {
              name: typedata.name,
              internalName: typedata.internalName,
              volume: typedata.volume,
              totalVolume: typedata.volume*inventoryData[i].stackQuantity,
              stackQuantity: inventoryData[i].stackQuantity,
              _id: inventoryData[i]._id

            }

             itemRows.push(row)

          }catch(e){
            console.log(e)
          }

        }

        this.itemCompoundList = {inventory: itemRows}


    },


    setActiveForm: function(formName)
    {

      this.activeForm = formName

    },

    setNeedsUpdate: function(needs){
      this.needsUpdate = needs
      this.requestItemsFromAPI()
    },

    requestItemsFromAPI: async function()
    {
      if(this.activeStationPanel == 'inventory' || this.needsUpdate) {
        this.needsUpdate=false
        console.log('request items from api ')
        console.log('this.activeStationPanel',this.activeStationPanel)
        var results = await this.dataRequestCallback({
          requestType:'inventory',
        containerEncapsulatingUnitId: this.stationUnitId//,
      //  containerOwnerPublicAddress:
      })

        this.inventoryDataChanged(results.data)
      }


    },

    onReceivedDataRequestResponse: function( response){

      //improves the snappiness of the hangar tab
      console.log('on re', response )

      if(response.requestType == 'destackItem'
      ||  response.requestType == 'trashItem'
      ||  response.requestType == 'stackAll'
      ||  response.requestType == 'salvageItem'
      ||  response.requestType == 'makeMarketOrder'
      ){
          this.setNeedsUpdate(true)
      }

    }





  }

}
</script>
