<template>
  <div >


    <form class="w-full max-w-lg"  v-if="targetData" >
  <div class="flex flex-wrap   ">
    <div class="w-1/2">

        <div> Input </div>

      <ItemList
      v-bind:itemArray="inputItemArray"
      v-bind:rightClickRowCallback="rightClickRowCallback"
       context="salvage"
      />


    <div class="hidden w-full  px-3 mb-6 md:mb-0">
      <label class="block uppercase tracking-wide text-gray-200 text-xs font-bold mb-2" for="grid-first-name">
        Item Name
      </label>
      <input disabled class="appearance-none  block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"  type="text"  >
      <p class=" hidden text-red-500 text-xs italic">Please fill out this field.</p>
    </div>
    <div class=" hidden w-full   px-3">
      <label class="block uppercase tracking-wide text-gray-200 text-xs font-bold mb-2" for="grid-last-name">
        Quantity To Salvage
      </label>
      <input disabled class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"  type="text" v-model="quantityToSalvage">
    </div>

      </div>
      <div class="w-1/2">
          <div> Output </div>


          <ItemList
          v-bind:itemArray="outputItemArray"
          v-bind:rightClickRowCallback="rightClickRowCallback"
           context="salvage"
          />

        </div>
  </div>


  <div class="mt-4" >
     <button @click="submit" class="float-right bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline cursor-pointer hover:bg-yellow-500" type="button">
        Salvage Items
      </button>
  </div>

</form>

  </div>
</template>


<script>




import GalaxyHelper from '../../../../shared/lib/GalaxyHelper'

import ItemList from '../../components/ItemList'

import SalvageHelper from '../../../../shared/lib/SalvageHelper'

const web3connectionconfig = require('../../../../shared/contracts/web3connection.config')

export default {


  name: 'SalvageItemForm',
  props: ['formSubmittedCallback','targetData'],
  components: {
    ItemList
  },
  data() {
    return {
      inputItemArray: null,

      quantityToSalvage: 0,
      outputItemArray: null

    }
  },
  created()
  {
    this.rebuild()
  },

  watch: {
   quantityToSalvage: function (val) {
       this.rebuild()
   }
 },

  methods: {

    rightClickRowCallback: function(){

    },

    rebuild: function(){

        this.outputItemArray=null

       this.inputItemArray = []

        let firstItem = this.targetData.item



        this.inputItemArray.push(firstItem)

      //  this.quantityToSalvage = this.targetData.stackQuantity

      console.log('rebuild', this.targetData )

        this.outputItemArray=[]
        for(var inputItem of this.inputItemArray)  {


          if(inputItem && !isNaN(inputItem.stackQuantity) && parseInt(inputItem.stackQuantity) >=1 ){
            let baseOutput = SalvageHelper.getItemsOutputForSalvaging(  inputItem  )


            if(baseOutput){



              for(var row of baseOutput){
                row.stackQuantity = row.quantity * parseInt(inputItem.stackQuantity);

                this.outputItemArray.push(row)
              }
            }


          }

        }




    },

    submit: function()
    {


      let formData = {
        requestType: 'salvageItem',
        item: this.targetData.item,
        quantity: this.quantityToSalvage

      }

      this.formSubmittedCallback( formData  )



      this.reset()

    },
    reset: function()
    {
      this.itemToSalvage=null;
      this.quantityToSalvage=0;
    },







  }

}
</script>
