  static getProductionData( internalName )
  {
    if(internalName)
    {
      return productiondata[ internalName ]
    }
    return null
  }


  static getBlueprintGroupData( groupName ){

      return blueprintgroups[groupName]
  }


  static getItemsInputForProduction(item)
  {
    let itemProductionData = ProductionHelper.getProductionData(item.internalName)
    if(itemProductionData)
    {
      return itemProductionData.input
    }
    return null
  }


  static async  getBaseBlueprintsAvailableAtStation(stationUnit, mongoInterface)
  {
    let stationGrid = await GalaxyHelper.findGridWithUUID(stationUnit.grid, mongoInterface)

    let constellationName = stationGrid.constellation ;

     let constellationData = GalaxyHelper.getConstellationTypeData( constellationName )


     let baseBlueprintGroups = constellationData.base_blueprint_groups

     return ProductionHelper.getBlueprintsFromBlueprintGroups( baseBlueprintGroups  )
  }

  static getBlueprintsFromBlueprintGroups( bpGroups ){

    let result =  []

    for(var group of bpGroups){

      let blueprintsInGroup = ProductionHelper.getBlueprintGroupData( group )



      result = result.concat( blueprintsInGroup )


    }

    return result

  }




