      static async   updateProductionSlot( productionSlot , mongoInterface ){


              console.log('updateProductionSlot', productionSlot)

         let itemTypeName = productionSlot.producingItemTypeName
         let quantityToProduce = productionSlot.quantityToProduce
         let stationUnitId = productionSlot.stationUnitId
         let playerPublicAddress = productionSlot.playerPublicAddress

         let stationItemContainer = await ItemHelper.getContainerFromPlayerAndUnitOwner( playerPublicAddress ,stationUnitId, mongoInterface )


         let itemTypeData = ItemHelper.getItemTypeByInternalName(itemTypeName)

         let inputItems = ProductionHelper.getTotalInputItemsToProduce(itemTypeName, quantityToProduce   )


         if(productionSlot.status == 'pending'){


                 //start escrowing items for the production slot
                 for(var item of inputItems){
                    let foundItem = await ProductionHelper.findItemInContainerWithQuantity(item.internalName, item.quantity, stationItemContainer._id, mongoInterface )

                    //try to escrow it .. if it is moveable
                    if( foundItem) { await ProductionHelper.escrowItemForProductionSlot( foundItem, item.quantity,  productionSlot, mongoInterface)  }
                 }

                 let numberOfRequiredInputItems = inputItems.length
                 let numberOfItemsEscrowed = 0;

                 //if they are all escrowed then modify the slot state ...
                 for(var item of inputItems){
                    let escrowedItem = await mongoInterface.findOne('items', {escrowedForProductionSlot: productionSlot._id, remainingQuantityToSpawn:null } )

                    if( escrowedItem) {
                       numberOfItemsEscrowed++;
                    }
                 }

                 //if all the right items are escrowed for the production slot, activate the production slot

                 if( numberOfItemsEscrowed >= numberOfRequiredInputItems ){
                   await mongoInterface.updateAndFindOne('productionSlot', {_id: productionSlot._id, status:'pending' } , {  status: 'active'  }) //has all items escrowed
                 }

           }


           let activeSlot = await mongoInterface.findOne('productionSlot',{_id: productionSlot._id, status:'active'});


           if( activeSlot ){

             let unixTimeStarted = activeSlot.startedAtUnixTime

             let currentTime = Date.now()

             let timeToProduce = ProductionHelper.getTimeToProduce( activeSlot )

             let hadEnoughTimeToProduce = (  currentTime -  unixTimeStarted   >=  timeToProduce  )


              //if the production slot is activated, delete all the input items which are not trying to stacksplit anymore

             await ProductionHelper.removeEscrowedItemsForActiveProductionSlot( productionSlot, mongoInterface )

             //if the items are removed and the status is active and enough unix time has elapsed , transition to complete and spawn the item with a childspawn lock id on the productionslot

             let escrowedItems = await mongoInterface.findAll('items', {escrowedForProductionSlot: productionSlot._id  } )
             if(escrowedItems.length == 0 && hadEnoughTimeToProduce ){
               console.log('all escrowed items for production removed - setting slot to complete ')
               await mongoInterface.updateAndFindOne('productionSlot', {_id: productionSlot._id, status:'active' } , {  status: 'complete', quantityOutputToSpawn: 0, childStackLockId: CryptoHelper.getRandomHash()  }) //has all items escrowed

               //spawn the output items

             }

            }


         let completedSlot = await mongoInterface.findOne('productionSlot',{_id: productionSlot._id, status:'complete'});


          if( completedSlot ){

            try{
              await ProductionHelper.spawnOutputItemForCompletedProductionSlot(completedSlot, mongoInterface )
            }catch(e){
                console.log(e)
            }

            let outputItem = await mongoInterface.findOne('items', {spawnLockId: completedSlot.childStackLockId} )

            console.log('outputItem', outputItem)
             //if the output item exists,  delete this production slot entirely
            if(outputItem){
              await mongoInterface.deleteOne('productionSlot',{_id: productionSlot._id , status:'complete'});

            }



         }

       }

       static getTimeToProduce(productionSlot){
         let productionData = ProductionHelper.getProductionData(productionSlot.producingItemTypeName)
         let timeFactor = +productionData.timeFactor

         let quantity = +productionSlot.quantityToProduce


         let timeMs = quantity * timeFactor * 1000

         return timeMs
       }

      static async escrowItemForProductionSlot( item, inputQuantity,  productionSlot, mongoInterface) {

        let quantityToEscrow = parseInt( inputQuantity )
        if(isNaN(quantityToEscrow)){
          console.trace('WARN: Tried to escrow item for production with input quantity NAN ')
          return null
        }

        let initialStackQuantity = item.stackQuantity
        let remainderQuantity = initialStackQuantity - quantityToEscrow

        if( inputQuantity <= 0 || inputQuantity > initialStackQuantity ){
          console.trace('WARN: Tried to escrow item for production with invalid input quantity ', item, inputQuantity)
           return null
        }

        let updatedItemResponse =  await mongoInterface.updateAndFindOne('items',  ItemHelper.getAllowedToMoveQueryConcatenation({ _id: item._id  }),
                                               {escrowedForProductionSlot: productionSlot._id, stackQuantity: quantityToEscrow,  remainingQuantityToSpawn: remainderQuantity, childStackLockId: CryptoHelper.getRandomHash()  })

         let updatedItem = updatedItemResponse.value

         //dont HAVE to do this but it speeds up the process
          await ItemHelper.spawnRemainderStackForItem(updatedItem,mongoInterface)

         return updatedItem
      }


      static async removeEscrowedItemsForActiveProductionSlot( productionSlot, mongoInterface ){

        let escrowedItems = await mongoInterface.findAll('items', {escrowedForProductionSlot: productionSlot._id })

        for(var item of escrowedItems){
          //again try to spawn remainder if it did not work when the slot was 'pending' for some reason
           await ItemHelper.spawnRemainderStackForItem(item,mongoInterface)
        }

       let result  = await mongoInterface.deleteMany('items', {escrowedForProductionSlot: productionSlot._id, remainingQuantityToSpawn: null })
       return result
      }

     //IMPLEMENT ME
     static async spawnOutputItemForCompletedProductionSlot(productionSlot, mongoInterface){
       //use the child lock Id so no dupes can happen

       let stationUnitId = productionSlot.stationUnitId
       let playerPublicAddress = productionSlot.playerPublicAddress

       let stationItemContainer = await ItemHelper.getContainerFromPlayerAndUnitOwner( playerPublicAddress ,stationUnitId, mongoInterface )

       if(!stationItemContainer){
         console.trace('WARN: tried to spawn output item to invalid container ', productionSlot)
         return null
       }

       if(!productionSlot.childStackLockId){
         console.trace('WARN: tried to spawn output item with no child stack lock id ', productionSlot)
         return null

       }


       let producedItemInsert = await ItemHelper.spawnNewItem({

         containerId: stationItemContainer._id,
         internalName: productionSlot.producingItemTypeName,
         stackQuantity: productionSlot.quantityToProduce,
         spawnLockId: productionSlot.childStackLockId   //indexed and unique so no dupes can happen

       }, mongoInterface)

       return producedItemInsert

     }








     static   getTotalInputItemsToProduce(itemTypeName, quantityToProduce     ){

       let itemTypeData = ItemHelper.getItemTypeByInternalName(itemTypeName)

       let inputItemsToProduceEach = ProductionHelper.getItemsInputForProduction( itemTypeData )

       console.log('meep input items ',itemTypeName ,  itemTypeData ,  inputItemsToProduceEach )

       let inputItemsTotal = inputItemsToProduceEach.map(function(inputItem){
           return { internalName: inputItem.internalName, quantity: (+inputItem.quantity * quantityToProduce)  };
       }  )

       return inputItemsTotal
     }


