     static async createNewProductionSlot(productionSlotData,mongoInterface){
       //if there is enough capacity..

       let currentUnixTime = Date.now()


       let newSlotId = await ProductionHelper.getNextAvailableSlotIdForPlayerAtStation( productionSlotData.playerPublicAddress,  productionSlotData.stationUnitId, mongoInterface )

       let insert = await mongoInterface.insertOne('productionSlot',{

         playerPublicAddress: productionSlotData.playerPublicAddress,
         stationUnitId: productionSlotData.stationUnitId,
          slotId: newSlotId,
         producingItemTypeName: productionSlotData.producingItemTypeName ,
         quantityToProduce: productionSlotData.quantityToProduce,
         startedAtUnixTime: currentUnixTime,
         status:'pending'
       } );


       let slot = await mongoInterface.findById('productionSlot',insert.insertedId )

       return slot



     }




     static async containerHasItems(inputItems, container , mongoInterface){

       for(var item of inputItems){
          let foundItem = await ProductionHelper.findItemInContainerWithQuantity(item.internalName, item.quantity, container._id, mongoInterface )

          if(!foundItem) return false
       }

       return true
     }


     static async findItemInContainerWithQuantity(inputItemInternalName, inputQuantity,  containerId, mongoInterface){
        inputQuantity = parseInt(+inputQuantity)

        if(isNaN(inputQuantity)){
          console.trace('WARN: input quantity NAN')
           return false;
        }


        let itemQuery = {internalName: inputItemInternalName, containerId: containerId,  stackQuantity: {$gte: inputQuantity}   }

        let safeItemQuery = ItemHelper.getAllowedToMoveQueryConcatenation(itemQuery)

       return await mongoInterface.findOne('items', safeItemQuery )
     }

