<template>
  <div @click="onClick"
  class="m-2 cursor-pointer border-2 custom-icon-border  rounded"
   style="width:32px;height:32px" v-bind:style="iconClass"  >




  </div>
</template>

<style>

.custom-icon-border:hover {
    border-color: #333 !important;
}

.custom-icon-border:active {
    border-color: #eaac4f !important;
}

</style>



<script>
import * as THREE from 'three'

import ImageHelper from '../../../shared/lib/math/ImageHelper'


export default {
  name: 'CustomIcon',
  props: [ 'iconImageData','clickedCallback', 'onClickParams'],
  data() {
    return {
      hidden: false,
      iconClass: ''

    }
  },
  created()
  {
    this.rebuild(this.iconImageData)
  },
  watch: {
      	iconImageData: function(newVal, oldVal) { // watch it
           this.rebuild( newVal )
        }
      },
  methods: {

    rebuild: function( iconImgData ){
      let image_path = "/assets/".concat(iconImgData.texturePath)
      let slot = iconImgData.iconSlot
      let tintColor = iconImgData.tintColor

      let computedBGPosition = ImageHelper.getBackgroundOffsetFromImageSlot(slot , 32, 5 )

      let borderColor = '#888';
      if(iconImgData.activated){
        borderColor='#57965c !important'
      }



        this.iconClass={
           backgroundImage: 'url("'+image_path+'"',
           backgroundRepeat: "no-repeat",
           backgroundPosition:computedBGPosition,
           backgroundSize: "160px 160px",

           backgroundColor: tintColor,
           backgroundBlendMode: 'multiply',
           borderColor: borderColor
         }

    //     console.log('iconimagedata',iconImgData,   this.iconClass)

    },

    setHidden: function(hidden){
        this.hidden=hidden

    },


    onClick: function( ){
      this.clickedCallback( this.onClickParams )
    }



  }

}
</script>
