<template>
<div v-if="subPanel=='hangarCargo'" class="mt-2 border-t-2 border-gray-200">
<div class="text-lg capitalize mt-2"> {{getShipName()}} Cargo   </div>

  <div class="flex">
    <div class="w-1/2">

      <ItemTable
        v-bind:itemCompoundList="stationInventoryItems"
        v-bind:rightClickRowCallback="handleRightClickRow"
        context='cargopanel_inventory'

       />

    </div>

    <div class="w-1/2">

      <ItemTable
        v-bind:itemCompoundList="shipCargoItems"
        v-bind:rightClickRowCallback="handleRightClickRow"
        context='cargopanel_cargo'

       />

    </div>
  </div>
</div>
</template>

<script>

  import ItemTable from '../../components/ItemTable.vue'

export default {


  name: 'HangarCargoPanel',
  props: ['subPanel','focusedShip','dataRequestCallback','stationUnitId','rightClickRowCallback'],
  components:{
    ItemTable
  },
  data() {
    return {
      stationInventoryItems: null,
      shipCargoItems: null,


    }
  },
  mounted(){

  },
  methods: {



    getShipName: function(){
      return this.focusedShip.basetype
    },

    update: async function(){
      let cargoInfoResponse = await this.requestShipCargoFromAPI()
      let inventoryInfoResponse = await this.requestStationInventoryFromAPI()

      this.stationInventoryItems = {}
      this.stationInventoryItems.inventory = inventoryInfoResponse.data

      this.shipCargoItems= {}
      this.shipCargoItems.cargo = cargoInfoResponse.data
      //console.log('cargodata',this.shipCargoItems)

    },

    requestShipCargoFromAPI: async function()
    {

        console.log('requestShipCargoFromAPI ')

        let results = await this.dataRequestCallback({
          requestType:'inventory',
          containerEncapsulatingUnitId: this.focusedShip._id
        })


          return results



    },

    requestStationInventoryFromAPI: async function()
    {
        console.log('requestStationInventoryFromAPI ')

        var results = await this.dataRequestCallback({
          requestType:'inventory',
            containerEncapsulatingUnitId: this.stationUnitId

          })
            return results

    },


    handleRightClickRow: function(tarData){
        tarData.panelFocusedUnit = this.focusedShip  //just inject this here
        this.rightClickRowCallback(tarData)
    }


  }


}

</script>
