<template>
  <div  :class="{ 'hidden': isHidden() }" class="absolute p-0 m-0" style="background:#111a; width:80%; height:80%; margin:   auto;top: 0; left: 0; bottom: 0; right: 0; ">


    <div class="m-4 p-4 h-full" style="margin:auto">


       <div class="  h-full bg-gray-800 font-roboto">

         <div class="block p-1 m-1 text-white w-full text-center" > Starmap </div>



         <div  id="starmapcanvas" v-on:mousemove="onMouseMove" ref="starmapcanvas" class="block relative" style="width:90%;height:90%;margin: 0 auto;" >

           <div class="absolute text-gray-200 p-2 capitalize" v-if="hoveredSystem" style="background:#111a; width:20%;   margin: auto; top: 0; left: 0;   " >

              <div> {{hoveredSystem.name}}  </div>
                <div class="text-xs text-blue-400 "> {{hoveredSystem.constellation}} Constellation </div>
           </div>


          </div>



      </div>
    </div>
  </div>

</template>
<script>
import * as THREE from 'three'
 import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import GalaxyHelper from '../../shared/lib/GalaxyHelper'

const solarsystemdata = require('../../shared/worlddata/solarsystems.json')
const jumpgateconnections = require('../../shared/worlddata/jumpgateconnections.json').jumpgate_connections

var raycaster = new THREE.Raycaster();

var hoverableMeshes=  []

export default {
  name: 'StarMap',
  data() {
    return {
      hidden: false,
      toggledOpen: false,
      hoveredSystem: null,


      myPossessedUnit: null,
      grid: null,

      renderer: null,
      scene: null,
      camera: null,
      controls:null,
      mouse: {x:0,y:0}
    }
  },

  mounted(){
    this.$nextTick(()=>{

        this.init()
    })
  },
  beforeDestroy(){


  },
  methods: {

    isHidden: function(){

      return  (this.hidden || !this.toggledOpen)


    },

    init:function(){

      console.log('init starmap')

      const RENDER_DISTANCE = 20000;
        const fov = 50;

        let canvasRect = document.getElementById('starmapcanvas').getBoundingClientRect()

    //    console.log('canvasRect', canvasRect)
      this.scene = new THREE.Scene()
      this.camera = new THREE.PerspectiveCamera(
        fov,
        canvasRect.width / canvasRect.height,
        0.1,
        RENDER_DISTANCE
      )

      this.camera.position.z = 10;


      this.renderer = new THREE.WebGLRenderer( { antialias: true   } );
      this.renderer.setPixelRatio( window.devicePixelRatio );
      this.renderer.setClearColor( 0x000000, 0.0 );
      this.renderer.setSize( canvasRect.width, canvasRect.height );
      document.getElementById('starmapcanvas').appendChild(this.renderer.domElement)
    //  this.$refs.starmapcanvas.appendChild(this.renderer.domElement)
      console.log('added starmap renderer ')

      this.scene.add(this.camera);

      this.controls = new OrbitControls( this.camera, this.renderer.domElement );

      this.controls.maxPolarAngle= 0.9
      this.controls.minPolarAngle= 0.3

      this.controls.maxAzimuthAngle= 0.4
      this.controls.minAzimuthAngle= -0.4


      this.controls.panSpeed = 0.2
      this.controls.minDistance = 100
      this.controls.maxDistance = 9000

      this.controls.update();

      this.buildStarMeshes()

      this.animate()

    },

    buildStarMeshes(){

    /*  while(this.scene.children.length > 0){
        this.scene.remove(scene.children[0]);
    }*/
  hoverableMeshes = []

    for(var solarsystem of Object.values(solarsystemdata)){

      let vec = solarsystem.systemLocationVector

      console.log('place star at ', vec )

      let starMesh = this.buildStarMesh( )
      starMesh.position.set( vec.x, vec.y , vec.z  )
      starMesh.userData = solarsystem

      starMesh.material.color.set( this.getBaseColorForStarmesh(starMesh)  );

      this.scene.add( starMesh );
      hoverableMeshes.push(starMesh)




    }



    for(var jgateconnection of jumpgateconnections){
      let systemA = solarsystemdata[jgateconnection.systemA]
      let systemB = solarsystemdata[jgateconnection.systemB]

    //  console.log('jgate ', systemA, systemB )

      let lineMesh = this.buildLine(systemA.systemLocationVector, systemB.systemLocationVector)
      this.scene.add( lineMesh );

    }


    },

    buildStarMesh(){
      let radius = 5
       var geometry = new THREE.SphereGeometry( radius, 32, 32 );
    //  var geometry = new THREE.BoxGeometry( 22, 1, 22 );
      var material = new THREE.MeshBasicMaterial( {color: 0xccccff} );
      var sphere = new THREE.Mesh( geometry, material );

      return sphere
    },



        buildLine( vecA, vecB ){

          var points = []
          points.push( new THREE.Vector3( vecA.x,vecA.y,vecA.z ) );
          points.push( new THREE.Vector3( vecB.x,vecB.y,vecB.z ) );



          var geometry = new THREE.BufferGeometry().setFromPoints( points );

          var material = new THREE.LineBasicMaterial( { color: 0xccccff } );

          var line = new THREE.Line( geometry, material );
          line.frustrumCulled = false
          return line
        },

    rebuild: function(){

      //  this.buildStarMeshes()

    /*  var geometry = new THREE.BoxGeometry( 22, 1, 22 );
      var material = new THREE.MeshBasicMaterial( {color: 0x00ff00} );
      var cube = new THREE.Mesh( geometry, material );
      this.scene.add( cube );

      console.log('add cube ')
    */
    },

    animate: function(){

      requestAnimationFrame(this.animate)

      if(!this.isHidden()){
        this.controls.update();

        this.renderer.render(this.scene, this.camera)
      }

    },


    onMouseMove: function(event){

      let canvasRect = document.getElementById('starmapcanvas').getBoundingClientRect()



      this.mouse.x = ((event.clientX-canvasRect.x) / canvasRect.width) * 2 - 1 ;
      this.mouse.y = -((event.clientY-canvasRect.y)  / canvasRect.height) * 2 + 1 ;


      var vector = new THREE.Vector3( this.mouse.x, this.mouse.y, 1 );
    	vector.unproject (this.camera );

    	var ray = new THREE.Raycaster( this.camera.position, vector.sub( this.camera.position ).normalize() );




      let clickables = hoverableMeshes


     var intersect;
     var intersects = ray.intersectObjects( clickables, true );
     if(typeof intersects == 'undefined' || !intersects.length)
     {
       intersect = undefined;

     }else{
       intersect = intersects[0].object;
       console.log('intersect', intersect )



       this.setNewHoveredMesh(  intersect )

     }



    },

    setNewHoveredMesh(mesh){

      for(var hoverable of hoverableMeshes){
      //  console.log('hov', hoverable )
          //hoverable.material.color.set(0xccccff);

           hoverable.material.color.set( this.getBaseColorForStarmesh(hoverable)  );


      }

      if(mesh){
        mesh.material.color.set(0xffffff);


         let systemData = mesh.userData


         this.hoveredSystem = systemData

      }


    },

    getBaseColorForStarmesh(mesh){

      let uuid = mesh.userData.uuid
      let playeruuid = null

      if(this.myPossessedUnit){
            playeruuid = this.myPossessedUnit.grid

           let celestials = mesh.userData.celestials

           for(var celestial of celestials){
             console.log('meep', celestial, this.myPossessedUnit)
             if(  celestial.uuid == this.myPossessedUnit.grid   ){
               return 0xffff88
             }

           }


      }

      return 0xaaaaaa


    },


    onWindowResize(){
      let canvasRect = document.getElementById('starmapcanvas').getBoundingClientRect()
      //console.log('canvasRect', canvasRect)

      this.camera.aspect = canvasRect.width / canvasRect.height;

      this.camera.updateProjectionMatrix();
      this.renderer.setSize( canvasRect.width, canvasRect.height );

    },

    entitiesChanged: function(myPossessedUnit, entities, gridData){

      if(myPossessedUnit)
      {
        this.myPossessedUnit=myPossessedUnit;
        this.grid = gridData

      }

      this.setNewHoveredMesh(null)///refresh colors of stars


    //  console.log('overview entities', this.entityList)

    },

    setToggledOpen: function(toggled){
      this.toggledOpen = toggled

      this.$nextTick(()=>{

         this.onWindowResize()
         this.rebuild()

         this.controls.reset()


         this.hoveredSystem=null //the system I am in ?
      })

    }




  }

}
</script>
