  static async setUnitActiveAction( activeActionData, mongoInterface )
  {


  //  let unitId
  let unit = await GalaxyHelper.findUnitById(  activeActionData.unitId, mongoInterface )

  let targetUnit = await GalaxyHelper.findUnitById(  activeActionData.targetUnitId , mongoInterface)

  if(!unit){
   return  {success:false, error: 'Could not set unit active action, unit null ' }
  }

    activeActionData.unitId = unit._id

    if(targetUnit){
      activeActionData.targetUnitId = targetUnit._id
    }


    if(activeActionData.unitId ){

      await mongoInterface.upsertOne('activeUnitAction',{unitId: unit._id}, activeActionData)

      return {success:true }
    }

    console.log('WARN: could not set unit active action', activeActionData)
    return  {success:false, error: 'Could not set unit active action ' }
  }

