<template>
  <div class="my-1  text-white  text-xs overflow-hidden px-2"   >

    <div v-if="!items "><div class="px-2"> ? </div></div>
      <div v-if="items && items.length ==0"><div class="px-2">Empty</div></div>

    <div v-if="items  ">

      <table class="table-auto text-xs text-gray-100 mt-4 border-t-0 border-b-0 w-full  px-2"   >

        <thead>
         <tr>
            <th class="px-1 py-1">Slot</th>
           <th class="px-1 py-1">Name</th>

            <th class="px-1 py-1">Quantity </th>
             <th class="px-1 py-1">Status</th>



         </tr>
       </thead>
       <tbody v-for="(array,key) in items">

           <th colspan="5" class="bg-blue-400 p-2 text-black capitalize w-full">  {{key}}   </th>
          <tr v-for="item in array" v-bind:key="item._id"  @mousedown.right="onRowClicked(item)" class="cursor-pointer">

                <td class="border px-4 py-4 text-right border-gray-700"> {{ item.slotId }} </td>
            <td class="border px-4 py-4 capitalize  border-gray-700 " > {{ item.name }}</td>
            <td class="border px-4 py-4 capitalize  border-gray-700 " > {{ item.quantityToProduce }}</td>
            <td class="border px-4 py-4 capitalize  border-gray-700 " > {{ item.status }}</td>

           </tr>

      </tbody>

      </table>

    </div>


  </div>
</template>

<style>


</style>



<script>
import * as THREE from 'three'

import ItemHelper from '../../../shared/lib/ItemHelper'
import ProductionHelper from '../../../shared/lib/ProductionHelper'

var updateTimer ;

export default {
  name: 'ActiveProductionSlotsTable',
  props: [ 'itemCompoundList','rightClickRowCallback' ,'context'],
  data() {
    return {
      hidden: false,
      items: []

    }
  },
  created()
  {
    this.disabled = false;

   this.rebuild(this.itemCompoundList)
    updateTimer =  setInterval(this.updateSlotRenderStatus, 500)
  },
  beforeDestroy(){

      clearInterval( updateTimer )
  },
   watch: {
      	itemCompoundList: function(newVal, oldVal) { // watch it
           this.rebuild( newVal )
        }
      },
  methods: {

  rebuild: function( itemCompoundList ){

    if(!itemCompoundList  ){
      this.items = null
      return
    }

    let keys = Object.keys(  itemCompoundList )

    if(!keys || keys.length ==0  ){
      this.items = null
      return
    }


    this.items = {}

    for(var key of keys){
      let slotsArray = itemCompoundList[key]

      /*
      productionslots [ { _id: 5f9203122900f81d48b6c1a4,
          playerPublicAddress: '0x7132c9f36abe62eab74cdfdd08c154c9ae45691b',
          stationUnitId: 5f91f8e2afa3f2115aad5d94,
          producingItemTypeName: 'basicarmorplating',
          quantityToProduce: 1,
          startedAtUnixTime: 1603404562239,
          status: 'complete',
          childStackLockId: '71b2ea40a7bf9000',
          quantityOutputToSpawn: 0 } ]

      */



  //    console.log('rebuild production slot   table', key ,  slotsArray)


      if(slotsArray){
        this.items[key] = []
        for(var slot of slotsArray){



           let itemTypeData = ItemHelper.getItemTypeByInternalName( slot.producingItemTypeName )

           slot.name = itemTypeData.name
           slot.internalName = itemTypeData.internalName

           let currentTime = Date.now()

           if(slot.status != 'complete' ){
             let currentProductionTime = (  currentTime -  slot.startedAtUnixTime )
             let timeToProduce = ProductionHelper.getTimeToProduce(slot)

             //console.log('currentProductionTime' , currentProductionTime )

            // console.log('timeToProduce' , timeToProduce )
             let timeRemainingMs = ( timeToProduce - currentProductionTime   )

             let timeRemainingSeconds = Math.max(  Math.floor(timeRemainingMs/1000) , 0 )
             slot.status = timeRemainingSeconds + ' s'

             if(timeRemainingSeconds == 0){
                slot.status = "finishing"
             }
           }




            this.items[key].push( slot )
          //  console.log('push', itemsArray[i])
        }

      }



    }//for each key




  },

      updateSlotRenderStatus:function(){
           this.rebuild(this.itemCompoundList)
      },

    onRowClicked: function(item)
    {


         this.rightClickRowCallback({itemType:'productionSlot', item:item, context:this.context})

    },


    setHidden: function(hidden){
        this.hidden=hidden

    },


    onClick: function( ){
      this.clickedCallback(   )
    }



  }

}
</script>
