<template>
  <div  v-if="!hidden"  class="absolute  py-4 px-6 right-0 top-0" style="background:#111a">

  <div @click="collapsed=!collapsed" class="text-centered w-full text-gray-100  cursor-pointer">  OVERVIEW </div>

    <table class="table-fixed text-xs text-gray-100 mt-4" v-if="!collapsed" >

      <thead>
       <tr>
         <th class="px-1 py-1">Name</th>
         <th class="px-1 py-1">Distance</th>
       </tr>
     </thead>
     <tbody>
      <tr v-for="item in entityList" v-bind:key="item._id">

        <td class="border px-1 py-1 capitalize cursor-pointer"
         v-bind:class="{'border-yellow-400':item.partiallyLocked&&!item.fullyLocked, 'border-green-400':item.fullyLocked,'bg-gray-300 text-green-600 ':(item.isPrimaryTarget&&item.fullyLocked) ,'bg-gray-100 text-gray-600':(item.isPrimaryTarget&&!item.fullyLocked)   }  "
         @mousedown.left="onRowLeftClicked(item)"
          @mousedown.right="onRowRightClicked(item)"
          v-on:dblclick="onRowDoubleClicked(item)"
          > {{ item.name }}</td>

        <td class="border px-1 py-1"> {{ item.distance }} </td>
      </tr>
    </tbody>

    </table>


  </div>

</template>


<style>

</style>
<script>
import * as THREE from 'three'


const lockedClass = " border-red-400 "


import GalaxyHelper from '../../shared/lib/GalaxyHelper'

import UnitHelper from '../../shared/lib/UnitHelper'

export default {
  name: 'OverviewPanel',
  props: ['rightClickRowCallback','localActionCallback','commandCallback'],

  data() {
    return {
      hidden: false,
      collapsed:false,
      entityList: [],
      myPossessedUnit: null,
      targetLocks: [],
      primaryTargetUnitId:null
    }
  },
  created(){
    this.lockedClass = lockedClass
  },
  methods: {

    onRowLeftClicked: function( unit ){
       this.localActionCallback('setPrimaryTarget',{unit: unit})


    },

    onRowRightClicked: function( unit ){
       this.rightClickRowCallback(unit)


    },

    onRowDoubleClicked: function( unit ){
      this.localActionCallback('setPrimaryTarget',{unit: unit})
      this.commandCallback('lock', {targetUnitId: unit._id}  )
    },


    entitiesChanged: function(myPossessedUnit, entities){

      if(myPossessedUnit)
      {
        this.myPossessedUnit=myPossessedUnit;

        this.entityList = []
        for(var i in entities)
        {
          if(!entities[i].dead && !entities[i].dockedInStation){
              this.entityList.push(  this.getTableDataForEntity( entities[i] ,myPossessedUnit ))
          }

        }
      }

      if(UnitHelper.unitIsDockedAtStation(myPossessedUnit ))
      {
        this.setHidden(true)
      }else{
        this.setHidden(false)
      }



    //  console.log('overview entities', this.entityList)

    },

    primaryTargetChanged: function( unit ){
      this.primaryTargetUnitId = unit._id
    },



    targetLocksChanged: function(targetLocks){
      this.targetLocks = targetLocks
    },


/*    unitIsFullyLocked: function(unit){

      for(var lock of this.targetLocks){

        if(lock.lockedUnitId.toString() == unit._id &&  lock.lockComplete){

          return true
        }

      }

        return false ;
    },

    unitIsPartialLocked: function(unit){

      for(var lock of this.targetLocks){

        if( lock.lockedUnitId.toString() == unit._id && !lock.lockComplete){

          return true
        }

      }

        return false ;
    },*/

    getTableDataForEntity(entity, myPossessedUnit)
    {
      var data = {}
      data._id = entity._id
      data.name =   GalaxyHelper.getNameForUnit( entity  )

      data.isPrimaryTarget = ( this.primaryTargetUnitId == entity._id )

      data.partiallyLocked = false
      data.fullyLocked = false
      for(var lock of this.targetLocks){

        if(lock.lockingUnitId.toString() == myPossessedUnit._id &&  lock.lockedUnitId.toString() == entity._id  ){
          if(lock.lockComplete){
              data.fullyLocked = true
          }else{
            data.partiallyLocked = true
          }

          break
        }
      }
      /*console.log('meep', entity )
      if(entity.unittype == 'celestial' ) //is celestial
      {
        data.name = GalaxyHelper.getEntityData( entity ).name

      }*/


      //distance is broken ?
      if(myPossessedUnit){
        var loc = new THREE.Vector3(entity.locationVector.x,entity.locationVector.y,entity.locationVector.z)
        data.distance = (loc.distanceTo( myPossessedUnit.locationVector )).toFixed(2)
        if(data.distance < 10){ data.distance = '--'}
      }

      return data
    },



        setHidden: function(hidden){
            this.hidden=hidden

        },



        playersChanged: function(myPlayer, playersOnGrid)
        {



        }


  }

}
</script>
