 import * as THREE from 'three'





export default class TargetReticleFactory
 {

   constructor(texLoader)
   {
     this.textureLoader = texLoader

   }


  async makeReticle( isActive )
  {

    var tex = await this.textureLoader.loadTexture('targetlock')
    if(isActive){
       tex = await this.textureLoader.loadTexture('targetlock_green')
    }

    var spriteMaterial = new THREE.SpriteMaterial( { map: tex , transparent: true} );
    var sprite = new THREE.Sprite( spriteMaterial );

    return sprite
  }





}
