
const CryptoHelper = require('./math/CryptoHelper')

const ItemHelper = require('./ItemHelper')

const salvagedata = require('../../shared/worlddata/items/salvagedata.json')

module.exports =  class SalvageHelper {



  static getSalvageData(item)
  {
    if(item)
    {
      return salvagedata[item.internalName]
    }
    return null
  }


  static getItemsOutputForSalvaging(item)
  {
    let itemSalvageData = SalvageHelper.getSalvageData(item)
    if(itemSalvageData)
    {
      return itemSalvageData.output
    }
    return null
  }




   static async startSalvagingItem(inputItem,  inputQuantity, mongoInterface){

  //   let newContainerId = newContainer._id

     let item = await mongoInterface.findById('items', inputItem._id)

     let quantityToSalvage = parseInt(+inputQuantity)

     if(isNaN(quantityToSalvage)){
       return {success:false, error: 'cannot salvage that quantity '}
     }


     if(!ItemHelper.allowedToMoveItem(item)){
       console.trace('Error: cannot salvage item while it is in that state ', item)
       return null
     }

     if(quantityToSalvage   > item.stackQuantity){
       return {success:false, error: 'cannot salvage that quantity '}

     }



     //console.log('quantityToSalvage', quantityToSalvage , item )

     if(quantityToSalvage && quantityToSalvage > 0){

        //mark the item and then call a method that has auto healing...
        //make sure we are allowed to move the item according to the database
         let response = await mongoInterface.updateAndFindOne('items', ItemHelper.getAllowedToMoveQueryById(item._id),
                                         {quantityToSalvage: quantityToSalvage }  )

          let newItem = response.value
         await SalvageHelper.spawnRemainingSalvageForItem(newItem, mongoInterface)

         return newItem
     }

     return null
   }




   static async spawnRemainingSalvageForItem(item, mongoInterface){

  //   console.log('meep spawnRemainingSalvageForItem', item )
     let quantityMultiplier = item.quantityToSalvage

     if(!quantityMultiplier  || parseInt(quantityMultiplier) == 0){

         await mongoInterface.updateCustomAndFindOne('items', {_id: item._id}, { $unset: {quantityToSalvage: undefined } }    )

       console.log('WARN: found quantity to salvage of 0 ')
       return
     }

     let spawnedItems = []

     let outputItems = SalvageHelper.getItemsOutputForSalvaging( item )

     for(var output of outputItems){

       let totalQuantity = ( parseInt(output.quantity) * parseInt(quantityMultiplier) )

       //the DB will not allow duplicates to be produced
       try{
         let newItemInsert = await ItemHelper.spawnNewItem({

           containerId: item.containerId,
           internalName: output.internalName,
           beingSalvagedFromItemId: item._id,
           stackQuantity: totalQuantity,
           spawnLockId: CryptoHelper.getRandomHash()  //indexed and unique so no dupes can happen

         }, mongoInterface)

         let newItem = await mongoInterface.findById('items', newItemInsert.insertedId)

         spawnedItems.push(newItem)
       }catch(e){
         console.trace('could not spawn duplicate salvage output (duplicate?)', e )
       }

     }

     //make them all - dont worry about double inserts bc mongo protects us

     let existingOutputs = await mongoInterface.findAll('items',{beingSalvagedFromItemId: item._id })



     //once we know they all exist, delete the original
     if(existingOutputs.length >= outputItems.length  ){
       console.log('all salvage outputs found in database ', item )
        let success =  await mongoInterface.deleteOne('items', {_id: item._id}   )

     }

     for(var newSpawnedItem of spawnedItems){

        await SalvageHelper.finishSalvageItemOutput(newSpawnedItem, mongoInterface)
     }



   }



   //if we notice that the original doesnt exist (point at via beingSalvagedFromItemId) then we remove it from those items
   static async finishSalvageItemOutput(outputItem, mongoInterface){

     console.log('finishSalvageItemOutput', outputItem)

     let item = await mongoInterface.findById('items', outputItem._id)

    //  console.log('item', item)

     if(item && item.beingSalvagedFromItemId){
       let parentItem = await mongoInterface.findById('items',item.beingSalvagedFromItemId)

       console.log('parentItem', parentItem)

       if(!parentItem){
         let newItem = await mongoInterface.updateCustomAndFindOne('items', {_id: item._id}, { $unset: {beingSalvagedFromItemId: undefined } }    )

       }

     }

   }




}
