

const iconmap = require('../../worlddata/textures/iconsmap.json')

module.exports =  class ImageHelper {

  static getBackgroundOffsetFromImageSlot(slot, tileWidth, tilesPerRow){

    let tileX = Math.floor(slot % tilesPerRow);
    let tileY = Math.floor(slot / tilesPerRow);

    let pixelX = Math.floor(-1*tileX*tileWidth).toString()
    let pixelY = Math.floor(-1*tileY*tileWidth).toString()

    return pixelX.concat('px ').concat(pixelY).concat('px')

  }

  static getIconDetailsFromIconName( iconName ){

    return iconmap[iconName]

  }


}
