import * as THREE from 'three'

export default class SkyBox {


/*
Should be preloading these with textureloader !

GOOD SKYBOXES:

corona
redeclipse
simplenebula
purplenebula
greennebula
bluenebula
darknebula



Borked:
blueshift
redshift


FIXING SKYBOXES

Make sure
top
right
bottom
looks good in gimp [refer to redeclipse in gimp ]

**for the nebulas from unity.. rotate top 90 CCW and bot 90 CW
*/

// See this but dont use left as the indicator, use right
/// https://codinhood.com/post/create-skybox-with-threejs

 constructor(texLoader){
   this.texLoader= texLoader
 }



 async preloadAssets(){

   let skyboxnames = ['corona','redeclipse',
 'simplenebula','bluenebula','greennebula','purplenebula','darknebula']

   for(var name of skyboxnames){
         await this.createMaterialArray(name)  //preload in texloader
   }


 }

 async getMesh(skyboxName)
 {
   const skyboxGeo = new THREE.BoxGeometry(8000, 8000, 8000);


 //  let skyboxImage = "bluenebula2";
   const materialArray = await this.createMaterialArray(skyboxName);

   const skybox = new THREE.Mesh(skyboxGeo, materialArray);

   return skybox
 }

 async getMaterial(skyboxName){
   return await this.createMaterialArray(skyboxName);
 }




 async createMaterialArray(filename) {
   const skyboxImageTextureDataArray = this.getSkyboxImageTextureData(filename);

let materialArray  = []
for (var imgData of skyboxImageTextureDataArray ){

 let texture = await this.texLoader.loadTexture(imgData.shortName, imgData.fullPath)

 let mat = new THREE.MeshBasicMaterial({ map: texture, side: THREE.BackSide }); // <---



 materialArray.push( mat )

}


 //  console.log( materialArray )
   return   materialArray;
 }



   getSkyboxImageTextureData(filename) {
     const basePath = "../assets/skybox/";
     const baseFilename = basePath + filename;
     const fileType = ".png";
     var sides =  ["front", "back", "top", "bottom", "right", "left"]


     const outputData = sides.map(side => {
       let fullPath =  baseFilename + "/" + side + fileType;
       let shortName = filename + '_' + side

       return {shortName: shortName, fullPath: fullPath }
     });
     return outputData;
   }


}
