 import * as THREE from 'three'




 var texturesmap = require('../../../shared/worlddata/textures/texturesmap.json')

 var spritesmap = require('../../../shared/worlddata/textures/spritesmap.json')

import AnimatedSpriteTexture from './AnimatedSpriteTexture'

export default class AnimatedSpriteFactory
 {

   constructor(texLoader)
   {
     this.textureLoader = texLoader

   }



  async makeSprite( spriteName )   // 'ship_death_explosion'
  {

    let spriteData = spritesmap[spriteName]

     /*{
     textureName:'explosions_1'
      tilesHoriz: 4,
      tilesVert: 2,
      minTile: 0,
      maxTile: 7,
      loop:false
    }*/


    var tex = await this.textureLoader.loadTexture(spriteData.textureName) //this clones -- thankfully
    var animatedTex = new AnimatedSpriteTexture( tex , spriteData  )

    //THIS WORKS
  //  tex = await this.textureLoader.loadTexture('explosions_1')

    var spriteMaterial = new THREE.SpriteMaterial( { map: tex , transparent: true} );
    var sprite = new THREE.Sprite( spriteMaterial );

  //  var spriteMaterial = new THREE.SpriteMaterial( { map: tex , transparent: true} );
  ///  var sprite = new THREE.Sprite( spriteMaterial );


    //this lets the entityyManager call 'sprite.updateTextureOneTick' to advance the animation
    sprite.updateTextureOneTick = animatedTex.getUpdateCallback();

    console.log('sprite', sprite)

    return   sprite
  }





}
