<template>
  <div v-if="activeStationPanel=='hangar'" >



    <div v-if="!subPanel" class="flex">

      <div class="w-1/2">

      <div class="text-lg"> Hangar   </div>

      <table class="table-fixed text-xs text-gray-100 mt-4 border-t-0 border-b-0"   >

        <thead>
         <tr>
            <th class="px-1 py-1">Icon</th>
           <th class="px-1 py-1">Base type</th>
           <th class="px-1 py-1">Volume (m3)</th>
           <th class="px-1 py-1">Est. Value</th>
         </tr>
       </thead>
       <tbody>
        <tr v-for="ship in shipRows" v-bind:key="ship._id"  @mousedown.right="onRowClicked(ship)" class="cursor-pointer" :class="{'bg-blue-700': (myPossessedUnit._id == ship._id)}">
          <td class="border px-4 py-4 text-right border-gray-700">   </td>

          <td class="border px-4 py-4 capitalize  border-gray-700 " > {{ ship.basetype }}</td>



        </tr>
      </tbody>

      </table>


    </div>
    <div class="w-1/2 text-sm">

      <div v-if="canRequestStarterShip()">

       <p>  You seem to have lost your ship.   You may board a new ship requisitoned from your home system.  </p>

        <div @click="clickedButton('boardStarterShip')" class="cursor-pointer m-2 p-1 bg-yellow-500 text-black text-sm inline-block">  Board My Corvette </div>


      </div>

    </div>

    </div>

      <div v-if="subPanel">
        <div class="p-1 m-1 text-sm cursor-pointer bg-yellow-500 text-black rounded  inline" @click="clickedButton('closeSubpanel')"> Back </div>

          <HangarCargoPanel
            v-bind:subPanel="subPanel"
            v-bind:focusedShip="focusedShip"
            v-bind:dataRequestCallback="dataRequestCallback"
            v-bind:stationUnitId="stationUnitId"
            v-bind:rightClickRowCallback="rightClickRowCallback"
            ref="hangarcargopanel"
            />

            <HangarFittingPanel
              v-bind:subPanel="subPanel"
            v-bind:focusedShip="focusedShip"
            v-bind:dataRequestCallback="dataRequestCallback"
            v-bind:stationUnitId="stationUnitId"
            v-bind:rightClickRowCallback="rightClickRowCallback"
            ref="hangarfittingpanel"
            />

      </div>


  </div>
</template>


<script>


/*
there will be submenu panels in here for :

cargo inside of ship
fitting

*/

import HangarCargoPanel from './hangar/HangarCargoPanel'
import HangarFittingPanel from './hangar/HangarFittingPanel'

import GalaxyHelper from '../../../shared/lib/GalaxyHelper'

var requestTimer;
export default {


  name: 'HangarPanel',
  props: ['stationPanelIsHidden','rightClickRowCallback','dataRequestCallback','stationUnitId','myPossessedUnit','activeStationPanel'],
  components: {
    HangarCargoPanel,HangarFittingPanel
  },
  data() {
    return {
      shipRows: [],

      needsUpdate: false,
      subPanel: null,
      focusedShip: null

    }
  },
  mounted(){
      this.update()
      requestTimer = setInterval( this.update, 5000 )
  },
  beforeDestroy(){

      clearInterval( requestTimer )
  },
  methods: {



    clickedButton: async function(buttonName)
    {
      if(buttonName == 'closeSubpanel'){
          this.subPanel=null
      }


      if(buttonName == 'boardStarterShip'){

        let result = await this.dataRequestCallback({
          requestType:'boardStarterShip'
        })

        console.log('result' , result)

      }

    },

    onRowClicked: function(ship)
    {
        console.log('on row clicked ')



         this.rightClickRowCallback({item: ship, itemType:'ship', context:'hangar'})

    },

    setNeedsUpdate: function(needs){
      this.needsUpdate = needs
      this.update()
    },

    update: async function()
    {
    //  console.log('requestShipsFromAPI ',this.activeStationPanel)

      console.log('this.stationPanelIsHidden', this.stationPanelIsHidden)
        if(this.stationPanelIsHidden){
          return
        }

      if(this.activeStationPanel == 'hangar' || this.needsUpdate) {
        this.needsUpdate=false

          console.log('  this.$refs',  this.$refs)

        if(this.subPanel=='hangarCargo' && this.$refs.hangarcargopanel){
              this.$refs.hangarcargopanel.update()
        }
        if(this.subPanel=='hangarFitting' && this.$refs.hangarfittingpanel){

              this.$refs.hangarfittingpanel.update()
        }



        var results = await this.requestShipsFromAPI()

        this.shipDataChanged(results.data)
      }


    },

    requestShipsFromAPI: async function()
    {

        console.log('request ships from api ')
        console.log('this.activeStationPanel',this.activeStationPanel)

        let results = await this.dataRequestCallback({
          requestType:'dockedships',
          dockedInUnitId: this.stationUnitId
        })

          return results



    },

    shipDataChanged: function(shipsData)
    {



        this.shipRows = []

        for(var ship of shipsData)
        {

          this.shipRows.push(ship)

        }

    },

    setActiveForm: function(formName)
    {

      this.activeForm = formName

    },

    closeSubPanels: function(){
      this.subPanel = null
      this.focusedShip = null
    },

    openSubPanel: function(name, params){
      if(name == 'hangarFitting'){
        this.subPanel = name
        this.focusedShip = params.item
      }
      if(name == 'hangarCargo'){
        this.subPanel = name
        this.focusedShip = params.item
      }

      console.log('open sub panel', name )

      this.$nextTick(()=>{
          this.setNeedsUpdate(true)
        })



    },

    onReceivedDataRequestResponse: function( response){

      //improves the snappiness of the hangar tab
      console.log('on re', response )

      if(response.requestType == 'unequipItemFromShip'
      ||  response.requestType == 'equipItemToShip'
      ||  response.requestType == 'moveItemToShip'
      ||  response.requestType == 'moveItemToStation'

      ){
          this.setNeedsUpdate(true)
      }

    },

    canRequestStarterShip: function(){

      for(var ship of this.shipRows){

        if(ship.basetype != 'capsule'){
          return false
        }
      }

      return true
    }





  }

}
</script>
