<template>
  <div v-if="!hidden"   class="absolute  py-4 px-6 left-0 right-0  " style=" bottom: 2%; min-height:160px; width:30%; margin: auto;">



    <TargetHud
    :class="{hidden: hidden}"
    ref="targethud"
    />

<div class="py-2 my-2 border-t-2 border-gray-400" style="background:#111a; min-height:140px;">

  <div class="hidden text-centered w-full text-gray-100  mb-4 capitalize">  Modules </div>

  <div ref="highsloticons" class="iconrow">
      <CustomIcon v-for="mod in equippedModules['high']"
        v-bind:iconImageData="{iconSlot:mod.textureIconSlot, texturePath:mod.texturePath, tintColor: mod.textureIconTint, activated:mod.engaged}"
        v-bind:clickedCallback="clickedIcon"
        v-bind:onClickParams="{equipmentSlotId: mod.slotId, engage: !mod.engaged}"
       />
  </div>

  <div ref="midsloticons" class="iconrow">
    <CustomIcon v-for="mod in equippedModules['mid']"
      v-bind:iconImageData="{iconSlot:mod.textureIconSlot, texturePath:mod.texturePath, tintColor: mod.textureIconTint, activated:mod.engaged}"
      v-bind:clickedCallback="clickedIcon"
      v-bind:onClickParams="{equipmentSlotId: mod.slotId, engage: !mod.engaged}"
     />
  </div>

  <div ref="lowsloticons" class="iconrow">
    <CustomIcon v-for="mod in equippedModules['low']"
      v-bind:iconImageData="{iconSlot:mod.textureIconSlot, texturePath:mod.texturePath, tintColor: mod.textureIconTint, activated:mod.engaged}"
      v-bind:clickedCallback="clickedIcon"
      v-bind:onClickParams="{equipmentSlotId: mod.slotId, engage: !mod.engaged}"
     />
  </div>
</div>

  </div>

</template>
<script>
import * as THREE from 'three'

import GalaxyHelper from '../../shared/lib/GalaxyHelper'
import UnitHelper from '../../shared/lib/UnitHelper'
import EquipmentHelper from '../../shared/lib/EquipmentHelper'

import ItemHelper from '../../shared/lib/ItemHelper'
import ImageHelper from '../../shared/lib/math/ImageHelper'
import TextureLoader from '../../client/js/TextureLoader'

import TargetHud from './TargetHud.vue'

import CustomIcon from './components/CustomIcon'


export default {
  name: 'ShipModulesPanel',
  data() {
    return {
      hidden: false,
      myPossessedUnit: null,
      compiledStats: {},
      equippedModules: {'high':[], 'mid':[], 'low':[]},
      primaryTargetUnitId:null
    }
  },
  props:['commandCallback'],
  components: {
     CustomIcon,TargetHud
  },

  methods: {

    clickedIcon: function(clickData){

      console.log('clicked icon',clickData)



      let commandData = {

            targetUnitId: this.primaryTargetUnitId,
            equipmentSlotId: clickData.equipmentSlotId,
            engage: clickData.engage


      }


      this.commandCallback('setModuleActivation',  commandData )


    },



    primaryTargetChanged: function( unit ){
      this.primaryTargetUnitId = unit._id
    },


    equipmentSlotsChanged: function(equipmentSlots ){



        this.rebuildIcons( equipmentSlots )
    },

    rebuildIcons: function( equipmentSlots )
    {
      let myUnitEquipmentSlots = {'high':[], 'mid':[], 'low':[]};

  //    console.log('myUnitEquipmentSlots', equipmentSlots)

      if(this.myPossessedUnit)
      {

        for(var slot of equipmentSlots)
        {



          let slotId = slot.slotId;
          let active = slot.active;
          let equipmentType = slot.equipmenttype;
          let unitId = slot.unitId;



          let equipmenttype = GalaxyHelper.getEquipmentTypeData( equipmentType )
          let itemtype = ItemHelper.getItemTypeByInternalName( equipmentType )

          let iconName = itemtype.iconName;
          let iconDetails = ImageHelper.getIconDetailsFromIconName(iconName)


          slot.slotType = EquipmentHelper.getSlotType(slot)

          try{
            slot.texturePath = TextureLoader.lookupAssetName(iconDetails.atlasname)
            slot.textureIconSlot = iconDetails.slot
            slot.textureIconTint = iconDetails.tint

          }catch(e)
          {
            console.log(e)
          }

          if(unitId == this.myPossessedUnit._id){


            myUnitEquipmentSlots[slot.slotType].push(slot);


          }


        }
      }

      this.equippedModules  = myUnitEquipmentSlots;
    //  console.log('mods',myUnitEquipmentSlots )

    },



    entitiesChanged: function(myPossessedUnit, entities){


      this.myPossessedUnit=myPossessedUnit;

      if(UnitHelper.unitIsDockedAtStation(myPossessedUnit ))
      {
        this.setHidden(true)
      }else{
        this.setHidden(false)
      }
    //  this.compiledStats = StatsHelper.getCompiledStatsAndVitals(myPossessedUnit)


      //show slider bar for armor stat, shield stat
    },


    setHidden: function(hidden){
        this.hidden=hidden

    },



    playersChanged: function(myPlayer, playersOnGrid)
    {




    }

  }

}
</script>
