<template>
  <div
   v-if="!hidden"
  :style="{top: offset.top, left:offset.left}"
  style="position:absolute;background-color:#111d;z-index:99"
  class="p-2 text-white hover:text-red text-sm mb-4 text-left">

  <div v-if="itemType == 'item' && context=='inventory'" >


    <button @click="command('startsell')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Sell On Market
    </button>

    <button @click="command('salvageItem')" class="block p-2 hover:bg-gray-600 w-full"   >
      Salvage
    </button>



    <button @click="command('stackAll')" class="block p-2 hover:bg-gray-600 w-full"   >
      Stack All
    </button>

    <button @click="command('destackItem')" class="block p-2 hover:bg-gray-600 w-full"   >
     Destack
    </button>

    <hr>

    <button @click="command('trashItem')" class="block p-2 hover:bg-gray-600 w-full"   >
      Trash
    </button>


  </div>

  <div v-if="itemType == 'item' && context=='loot'" >


    <button @click="command('lootitem')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Loot
    </button>



  </div>

  <div v-if="itemType == 'item' && context=='cargopanel_cargo'" >

    <button @click="command('moveItemToStation')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Move to Station Inventory
    </button>

  </div>

  <div v-if="itemType == 'item' && context=='cargopanel_inventory'" >

    <button @click="command('moveItemToShip')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Move to Ship Cargo
    </button>

  </div>

  <div v-if="itemType == 'item' && context=='fitting'" >

    <button @click="command('equipItemToShip')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Equip to Ship
    </button>

  </div>

  <div v-if="itemType == 'equipment' && context=='fitting'" >

    <button @click="command('unequipItemFromShip')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Unequip from Ship
    </button>

  </div>

  <div v-if="itemType == 'blueprint' && context=='production'" >

    <button @click="command('startProducingFromBlueprint')" class="block p-2 hover:bg-gray-600 w-full  "   >
      Produce From Blueprint
    </button>

  </div>

    <div v-if="itemType == 'order'" >
      <button @click="command('buyout')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Buyout
      </button>

      <button @click="command('orderdetails')" class="block p-2 hover:bg-gray-600 w-full  "   >
        View Order Details
      </button>

      <button v-if="canCancelOrder()" @click="command('cancelorder')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Cancel Order
      </button>
    </div>

    <div v-if="itemType == 'ship'" >

      <button @click="command('possessShip')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Board
      </button>


      <button @click="command('openFittingPanel')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Fitting
      </button>

      <button @click="command('openHangarCargoPanel')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Cargo Contents
      </button>

      <button  @click="command('repackageShip')" class="block p-2 hover:bg-gray-600 w-full  "   >
        Repackage
      </button>
    </div>




  </div>
</template>
<script>
import * as THREE from 'three'

import GalaxyHelper from '../../shared/lib/GalaxyHelper'

export default {
  name: 'ItemActionMenu',
  props: ['dataRequestCallback','localActionCallback'],
  components: {

  },
  data() {
    return {
      hidden: true,
      offset: {top:0, left:0},
      targettingData: {},

      targettedItem: null,
      panelFocusedUnit:null,
      itemType: null,
      context: null,

      myPlayer: null

    }
  },
  methods: {



    setHidden: function(hidden){
        this.hidden=hidden

    },


    playersChanged: function(myPlayer, playersOnGrid)
    {
      this.myPlayer = myPlayer



    },


    canCancelOrder: function(){

      if(this.myPlayer  )
      {
        let publicAddress = this.myPlayer.publicAddress

        return this.targettedItem && this.targettedItem.payTo && this.targettedItem.payTo == publicAddress

      }

      return false

    },

    setOffsets: function(offset){
        this.offset=offset

    },

    command: async function(cmdName){

    //  var targetId = this.targettingData.targetId;

      if(cmdName == 'startsell')
      {
         this.localActionCallback('startsell', {item: this.targettedItem } )   // 5f7aab8282f5ae6884aaa2ff ???
      }

      if(cmdName == 'salvageItem')
      {
         this.localActionCallback( 'salvageItem', {item: this.targettedItem } )
       }



      if(cmdName == 'cancelorder')
      {
        //an onchain event .. also sends tx data to server
         this.localActionCallback('cancelOrder',{order: this.targettedItem } )   // 5f7aab8282f5ae6884aaa2ff ???
      }

      if(cmdName == 'buyout')
      {
         this.localActionCallback( 'buyoutOrder', {order: this.targettedItem  } )
       }


       if(cmdName == 'openFittingPanel')
       {
          this.localActionCallback( 'openFittingPanel', {item: this.targettedItem } )
        }

      if(cmdName == 'openHangarCargoPanel')
      {
         this.localActionCallback( 'openHangarCargoPanel',{item: this.targettedItem } )
       }


       if(cmdName == 'destackItem')
       {
          this.localActionCallback( 'destackItem',{item: this.targettedItem } )
        }

        if(cmdName == 'trashItem')
        {
           this.localActionCallback( 'trashItem',{item: this.targettedItem } )
         }


         if(cmdName == 'startProducingFromBlueprint')
         {
          this.localActionCallback( 'startProducingFromBlueprint',{item: this.targettedItem } )
        }


       if(cmdName == 'stackAll')
       {
         let input = {
           requestType:'stackAll',
            item: this.targettedItem
           }

         let response = await this.dataRequestCallback(input )
        }




         if(cmdName == 'moveItemToStation')
         {
           let input = {
             requestType:'moveItemToStation',
              item: this.targettedItem,
              station: this.panelFocusedUnit
             }

           let response = await this.dataRequestCallback(input )
         }

         if(cmdName == 'moveItemToShip')
         {
           let input = {
             requestType:'moveItemToShip',
              item: this.targettedItem,
              ship: this.panelFocusedUnit
             }

            let response = await this.dataRequestCallback(input )
         }

         if(cmdName == 'equipItemToShip')
         {
           let input = {
             requestType:'equipItemToShip',
              item: this.targettedItem,
                ship: this.panelFocusedUnit
             }

          let response = await this.dataRequestCallback(input )
         }
         if(cmdName == 'unequipItemFromShip')
         {
           let input = {
             requestType:'unequipItemFromShip',
              item: this.targettedItem
             }

          let response = await this.dataRequestCallback(input )
         }

         if (cmdName == 'possessShip'){
           let input = {
             requestType:'possessShip',
              item: this.targettedItem
             }

            let response = await this.dataRequestCallback(input )

         }

         if(cmdName == 'repackageShip')
         {
           let input = {
             requestType:'repackageShip',
              item: this.targettedItem
             }

            let response = await this.dataRequestCallback(input )
         }

         if(cmdName == 'unpackageShip')
         {

         }




      this.setHidden(true)
    },


    buildTargettingData: function( tarData )  //entity null ?
   {


     this.targettingData = tarData
     console.log('built tar data', tarData)

     this.targettedItem = tarData.item
     this.panelFocusedUnit = tarData.panelFocusedUnit
     this.itemType = tarData.itemType
     this.context = tarData.context
   },




  }

}
</script>
