import * as THREE from 'three'


var texturecache = {}
var loader


var texturesmap = require('../../shared/worlddata/textures/texturesmap.json')

const preloadedAssets =  Object.keys(texturesmap)
/*['targetlock',
'targetlock_green',
'materials_sheet',
'weapons_sheet',
'modules_sheet' ]*/


export default class TextureLoader   {


 constructor(loadingStepCallback)
 {
   THREE.Cache.enabled = false

   loader = new THREE.TextureLoader();

   this.loadingStepCallback = loadingStepCallback;
 }


 async loadTexture( name, customPath ) {

   var cacheid =  (name)

   if(texturecache[cacheid]){

     var existing =  texturecache[cacheid].clone() //need a new id
        console.log('loaded texture from cache ',cacheid, existing)

     return existing
   }


   this.loadingStepCallback(name)


     var filepath

   if(!customPath){
     var assetpath = TextureLoader.lookupAssetName( name)

        filepath = '../assets/'+assetpath
   }else{
     filepath = customPath
   }


  console.log('loading..' ,filepath)

  //this DOES have to  be async
  let tex = await loader.load(filepath)



  //  texturecache[cacheid] = tex  //cache  Cant get this to work w skybox 
  return tex


 }


 static lookupAssetName( name)
 {

     return texturesmap[name].path.toString()


 }


 async preloadAssets()
 {
   for(var asset of preloadedAssets)
   {
     await this.loadTexture( asset )
   }


 }

}
