<template>
  <div v-if="!hidden && canLootTarget"   class="absolute  py-2 px-2   border-t-2 border-gray-400 " style="background:#111a; bottom: 40%; left:30%; height:200px; width:160px;  ">

  <div class="  text-centered w-full text-gray-100  mb-4 capitalize">  Loot </div>



    <div class="border-2 border-gray-400 overflow-y-auto" style="height:100px">

      <ItemList
      v-bind:itemArray="itemArray"
      v-bind:rightClickRowCallback="handleItemActionMenuCallback"
       context="loot"
      />
    </div>

    <div class="mt-2 p-1">
      <div class="cursor-pointer inline text-black bg-gray-300 p-1 mt-4 rounded text-xs" :class="{'text-gray-500': (!itemArray || itemArray.length==0)  }" @click="command('lootall')"> Loot All </div>
    </div>
  </div>

</template>
<script>
import * as THREE from 'three'

import GalaxyHelper from '../../shared/lib/GalaxyHelper'
import UnitHelper from '../../shared/lib/UnitHelper'
import EquipmentHelper from '../../shared/lib/EquipmentHelper'

import ItemHelper from '../../shared/lib/ItemHelper'
import ImageHelper from '../../shared/lib/math/ImageHelper'
import TextureLoader from '../../client/js/TextureLoader'

import ItemList from './components/ItemList'



export default {
  name: 'LootPanel',
  data() {
    return {
      hidden: false,///due to being docked
      canLootTarget: false,
      myPossessedUnit: null,

      lootingTargetUnit:null,
      itemArray: [],
      dataError: null
    }
  },
  props:['localActionCallback','commandCallback','dataRequestCallback','handleItemActionMenuCallback'],
  components: {
    ItemList
  },
  created(){

      setInterval( this.refresh.bind(this), 2000 )

  },
  methods: {

    clickedIcon: function(clickData){


    },

    refresh: async function(){
    //  console.log('refresh')




      if(this.canLootUnit(this.myPossessedUnit,this.lootingTargetUnit)){
        if(!this.canLootTarget){
          this.localActionCallback('openShipCargoPanel')
        }

          this.canLootTarget = true
      }else{
          this.canLootTarget = false
      }

      if(this.hidden || !this.canLootTarget){
        return
      }

      if(this.canLootTarget){
        ///this keep happening and happening every second
        console.log('request items for loot panel')
        //ask the server for the contents ...
        var results = await this.dataRequestCallback({
          requestType:'inventory',
          containerEncapsulatingUnitId: this.lootingTargetUnit._id,
          containerOwnerPublicAddress: this.lootingTargetUnit.owningPlayerAddress
        })


        //  this.itemArray = []
        this.inventoryDataChanged(results.data)

      }else{
        //show an error ?

        //  this.itemArray = []
      }

    },

    inventoryDataChanged:function(invData){
      //  console.log('inventoryDataChanged  ', invData )
      //if(invData && invData.length>0){
        this.itemArray = invData    //update our state


    //  }

    },



    canLootUnit: function(unit , lootedUnit ){

      if(!unit || !lootedUnit){
      //  console.log('cannot loot null.. ',unit ,lootedUnit )
        return false
      }

      let targetIsLootable = UnitHelper.unitHasService( lootedUnit, 'looting'  )
      let targetWithinServiceRange = UnitHelper.unitsWithinServiceRange(unit, lootedUnit )

    //  console.log('loot?', targetIsLootable , targetWithinServiceRange )
      return (targetIsLootable && targetWithinServiceRange)

    },

    lootingTargetChanged: function(unit){
    //  console.log('looting target changed ! ')

      this.lootingTargetUnit = unit;

      this.refresh()

    },


    entitiesChanged: function(myPossessedUnit, entities){


      this.myPossessedUnit=myPossessedUnit;

      if(UnitHelper.unitIsDockedAtStation(myPossessedUnit ))
      {
        this.setHidden(true)
      }else{
        this.setHidden(false)
      }


      //show slider bar for armor stat, shield stat
    },


    setHidden: function(hidden){
        this.hidden=hidden

    },

    resetPanel: function(hidden){
        this.lootingTargetUnit = null;

        this.refresh()

    },



    playersChanged: function(myPlayer, playersOnGrid)
    {



    },

    command: function(cmdName){

      if(cmdName == 'lootall')
      {

        if(!this.itemArray || this.itemArray.length==0) return;


         this.commandCallback('lootall', {
           containerEncapsulatingUnitId: this.lootingTargetUnit._id,
           containerOwnerPublicAddress: this.lootingTargetUnit.owningPlayerAddress
       })

      }

    }




  }

}
</script>
